
import {
  reactive,
  toRefs,
  onMounted,
  onBeforeUnmount,
  watch,
  defineComponent,
} from "vue";
import { Service } from "~/utils/service";
export default defineComponent({
  name: "EpsplanetShadow",
  props: [],
  setup() {
    let sunwayearth: any;
    const data = reactive({
      label: {
        darkness: "暗度",
        maximumDistance: "最大距离",
        normalOffset: "法向量偏移",
        terrainShadow: "地形阴影",
        softShadows: "软阴影",
      },
      terrainShadow: true,
      softShadows: false,
      size: 2048,
      darkness: 0.3,
      maximumDistance: 5000.0,
      normalOffset: true,
    });
    watch(
      () => data.terrainShadow,
      (val) => {
        if (sunwayearth)
          sunwayearth.effect.shadow.terrainShadow = val;
      }
    );
    watch(
      () => data.softShadows,
      (val) => {
        if (sunwayearth)
          sunwayearth.effect.shadow.softShadows = val;
      }
    );
    watch(
      () => data.normalOffset,
      (val) => {
        if (sunwayearth)
          sunwayearth.effect.shadow.normalOffset = val;
      }
    );
    watch(
      () => data.darkness,
      (val) => {
        if (sunwayearth)
          sunwayearth.effect.shadow.darkness = val;
      }
    );
    watch(
      () => data.maximumDistance,
      (val) => {
        if (sunwayearth)
          sunwayearth.effect.shadow.maximumDistance = val;
      }
    );
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      sunwayearth.effect.shadow.enabled = true;
    });
    onBeforeUnmount(() => {
      sunwayearth.effect.shadow.enabled = false;
    });
    const refData = toRefs(data);
    return {
      ...refData,
    };
  },
});
