
/* eslint-disable */
import {
  reactive,
  toRefs,
  onMounted,
  onBeforeUnmount,
  defineComponent,
  getCurrentInstance,
} from "vue";
import { Service } from "~/utils/service";
export default defineComponent({
  name: "EpsplanetModelPicker",
  setup() {
    // 获取earth
    let sunwayEarth: any;
    const { proxy } = getCurrentInstance() as any;
    const thisVue = proxy;
    const data = reactive({
      tableData: [
        {
          property: "",
          value: "",
        },
      ],
    });
    let handler: any, testmodel: any;
    onMounted(async () => {
      sunwayEarth = await Service.instance.getEarth();
      if (!handler) {
        init();
        initPick();
      } else {
        del();
      }
    });
    onBeforeUnmount(() => {
      del();
    });
    function init() {
      sunwayEarth.interaction.picking.enabled = true; // 开启拾取操作
      // sunwayEarth.interaction.picking.clickedColor = [0, 0.5, 0.6, 1];
      // sunwayEarth.interaction.picking.hoveredColor = [1, 0.8, 0.9, 1];
      sunwayEarth.czm.viewer.scene.globe.depthTestAgainstTerrain = true; // 开启深度监测
      // 加载测试模型数据
      // sunwayEarth.sceneTree.root.children.push(
      //   {
      //     ref: "test",
      //     czmObject: {
      //       name: "大雁塔模型",
      //       xbsjType: "Tileset",
      //       url: "//lab.earthsdk.com/model/f15b9e90ac2d11e99dbd8fd044883638/tileset.json",
      //       // xbsjUseOriginTransform: false, //xbsjUseOriginTransform: false的情况下，xbsjPosition才有效
      //       // xbsjPosition: [
      //       //   (124.461089 / 180) * Math.PI,
      //       //   (40.444334 / 180) * Math.PI,
      //       //   0,
      //       // ],
      //     },
      //   },
      //   // {
      //   //   ref: "test2",
      //   //   czmObject: {
      //   //     name: "自动武器",
      //   //     xbsjType: "Tileset",
      //   //     url: "model/automated-weapon/tileset.json",
      //   //     xbsjUseOriginTransform: false, //xbsjUseOriginTransform: false的情况下，xbsjPosition才有效
      //   //     xbsjPosition: [
      //   //       (124.475435 / 180) * Math.PI,
      //   //       (40.444334 / 180) * Math.PI,
      //   //       0,
      //   //     ],
      //   //   },
      //   // }
      // );
      // testmodel = sunwayEarth.sceneTree.$refs.test;
      // testmodel.czmObject.flyTo();
    }
    function initPick() {
      // 注册点击事件：左键点击
      // handler = new Cesium.ScreenSpaceEventHandler(
      //   sunwayEarth.czm.viewer.scene.canvas
      // );
      // handler.setInputAction((event) => {
      //   if (sunwayEarth.interaction.picking.enabled) {
      sunwayEarth.tilesetCollection.forEach((modelset) => {
        modelset.onclick = (pickedObject) => {
          data.tableData = [];
          if (Cesium.defined(pickedObject)) {
            if (pickedObject.getPropertyNames) {
              let propertyCollection = pickedObject.getPropertyNames();
              // console.log(propertyCollection);
              propertyCollection.forEach((pro) => {
                var obj = {
                  property: pro,
                  value: pickedObject.getProperty(pro),
                };
                data.tableData.push(obj);
              });
            } else return;
            // let objName = pickedObject.getProperty("name");
            // let pickedPosition =
            //   sunwayEarth.czm.viewer.scene.pickPosition(event.position);
          }
        };
      });
      // }
      // }, Cesium.ScreenSpaceEventType.LEFT_CLICK);
    }
    function initPick2() {
      // Cesium后处理
      var silhouetteBlue =
        Cesium.PostProcessStageLibrary.createEdgeDetectionStage();
      silhouetteBlue.uniforms.color = Cesium.Color.BLUE;
      silhouetteBlue.uniforms.length = 0.01;
      silhouetteBlue.selected = [];

      var silhouetteGreen =
        Cesium.PostProcessStageLibrary.createEdgeDetectionStage();
      silhouetteGreen.uniforms.color = Cesium.Color.LIME;
      silhouetteGreen.uniforms.length = 0.01;
      silhouetteGreen.selected = [];

      sunwayEarth.czm.scene.postProcessStages.add(
        Cesium.PostProcessStageLibrary.createSilhouetteStage([
          silhouetteBlue,
          silhouetteGreen,
        ])
      );

      sunwayEarth.sceneTree.root.children.forEach((modelset) => {
        if (modelset && modelset.czmObject.xbsjType == "Tileset") {
          console.log("name:", modelset.czmObject.name);
          // 拾取事件定制
          modelset.czmObject.onclick = (pickedObject) => {
            if (
              !silhouetteBlue.selected ||
              silhouetteBlue.selected[0] !== pickedObject
            ) {
              silhouetteBlue.selected = [pickedObject];
            }
            // return false; 是否禁用点选效果？
            data.tableData = [];
            if (Cesium.defined(pickedObject)) {
              let propertyCollection = pickedObject.getPropertyNames();
              // console.log(propertyCollection);
              propertyCollection.forEach((pro) => {
                var obj = {
                  property: pro,
                  value: pickedObject.getProperty(pro),
                };
                data.tableData.push(obj);
              });
            }
          };
          // 空白界面点击事件定制
          modelset.czmObject.onclickout = () => {
            if (silhouetteBlue.selected && silhouetteBlue.selected.length > 0) {
              silhouetteBlue.selected = [];
            }
            // return false; 是否禁用点选效果？
          };
          // 鼠标移过事件定制
          modelset.czmObject.onmouseover = (pickedObject) => {
            if (
              !silhouetteGreen.selected ||
              silhouetteGreen.selected[0] !== pickedObject
            ) {
              silhouetteGreen.selected = [pickedObject];
            }
            // return false; 是否禁用点选效果？
          };
          // 鼠标移出模型事件定制
          modelset.czmObject.onmouseout = () => {
            if (
              silhouetteGreen.selected &&
              silhouetteGreen.selected.length > 0
            ) {
              silhouetteGreen.selected = [];
            }
            // return false; 是否禁用点选效果？
          };
        }
      });
    }

    function del() {
      if (handler) {
        handler = handler && handler.destroy();
      }

      if (testmodel) {
        sunwayEarth.sceneTree.root.children.remove(testmodel);
        testmodel = undefined;
      }
      sunwayEarth.interaction.picking.enabled = false; // 关闭拾取操作
      sunwayEarth.czm.viewer.scene.globe.depthTestAgainstTerrain = false; // 关闭深度监测
    }
    const refData = toRefs(data);
    return {
      ...refData,
      init,
      initPick,
      initPick2,
      del,
    };
  },
});
