
import {
  reactive,
  toRefs,
  onMounted,
  onBeforeUnmount,
  defineComponent,
} from "vue";
import { Service } from "~/utils/service";
export default defineComponent({
  name: "EpsplanetSilhouette",
  props: [],
  setup() {
    let sunwayearth: any;
    const data = reactive({
      title: "轮廓颜色",
      length: 0.5,
      uicolor: "rgba(11, 211, 34, 0.8)",
    });
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      sunwayearth.postProcess.silhouette.enabled = true;
    });
    onBeforeUnmount(() => {
      sunwayearth.postProcess.silhouette.enabled = false;
    });
    function changeColor(val: string) {
      let v = val.split("(")[1].split(")")[0].split(",");
      let cc: number[] = [];
      v.forEach((c: any, i: number) => {
        if (i == 3) {
          cc.push(Number(c));
        } else {
          cc.push(Number(c) / 255.0);
        }
      });
      sunwayearth.postProcess.silhouette.color = cc;
    }
    const refData = toRefs(data);
    return {
      ...refData,
      changeColor,
    };
  },
});
