
/* eslint-disable */
import getExtremum from "sdk/XbsjEarth-Plugins/getExtremum";

import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
  computed,
  watch,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetHaplochromatization",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    const thisVue = proxy;
    let _classificationpolygon: any;
    const data = reactive({
      tabCheck: "1", //切换
      form: {
        name: "",
        topHeight: 0,
        botttomHeight: 0,
        color: [1, 1, 0, 0.5],
        textarea: "//p对象即为当前模型\nalert('名称：'+p.name);",
      }, //表单
      selectOptions: [
        {
          label: "单击",
          value: "onclick",
        },
      ], //事件
      operationEvent: undefined, //选择的事件
      editing: undefined, //编辑
      _disposers: [] as any[],
      selectColor: "rgba(255, 255, 0, 0.5)",
      _classificationpolygon,
    });
    watch(
      // 监听颜色属性，将字符串转为数组
      () => data.selectColor,
      (val) => {
        let arr: any = val.substring(5, val.length - 1).split(", ");
        for (let index = 0; index < arr.length; index++) {
          if (index == 3) {
            arr[index] = Number(arr[index]);
          } else {
            arr[index] = arr[index] / 255;
          }
        }
        data.form.color = arr;
      }
    );
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      sunwayearth.sceneTree.root.children.push({
        ref: "tileset",
        czmObject: {
          xbsjType: "Tileset",
          xbsjGuid: "b4137394-d346-4434-a01e-5b2d3730ec9f",
          name: "三维瓦片1x",
          url: "//lab.earthsdk.com/model/f15b9e90ac2d11e99dbd8fd044883638/tileset.json",
          lightColor: null,
          specularEnvironmentMaps: null,
          xbsjPosition: [
            1.9016974701882112, 0.5972325152147303, 425.8641913624607,
          ],
          xbsjFlattenGuid: "93916e9b-82dd-4a56-b15e-27303b08e781",
          xbsjClippingPlanes: {
            planes: ["9ab55d98-32ed-49ad-9004-1940f4903b68"],
          },
          skipLevelOfDetail: false,
        },
      });
      init();
    });
    onBeforeUnmount(() => {
      del();
    });
    function init() {
      sunwayearth.interaction.picking.enabled = true;
      var classificationpolygon = new XE.Obj.ClassificationPolygon(sunwayearth);
      data._classificationpolygon = classificationpolygon;
      classificationpolygon.creating = true;
      data._disposers = [];
      data._disposers.push(
        XE.MVVM.bind(thisVue, "editing", classificationpolygon, "editing"),
        XE.MVVM.bind(
          thisVue,
          "form.topHeight",
          classificationpolygon,
          "height"
        ),
        XE.MVVM.bind(
          thisVue,
          "form.bottomHeight",
          classificationpolygon,
          "extrudedHeight"
        ),
        XE.MVVM.bind(thisVue, "form.color", classificationpolygon, "color")
      );
    }
    function creatNewClass() {
      clear();
      init();
    }
    function save() {
      if (data._classificationpolygon) {
        data._classificationpolygon.name = data.form.name;
        if (data.operationEvent) {
          if (data.operationEvent == "onclick") {
            data._classificationpolygon.evalString = `
            p.onclick=()=>{
              ${data.form.textarea}
            }
            `;
          }
        }
        let json = data._classificationpolygon.toJSON();
        let classificationpolygon = new XE.Obj.ClassificationPolygon(
          sunwayearth
        );
        classificationpolygon.xbsjFromJSON(json);
        const sceneObject = new XE.SceneTree.Leaf(classificationpolygon);
        sunwayearth.sceneTree.root.children.push(sceneObject);
        confirmHaplochromatization(sceneObject);
        let timeOut = setTimeout(() => {
          clear();
          clearTimeout(timeOut);
        }, 1000);
      }
    }
    //单体化保存组
    function confirmHaplochromatization(czmObject: any) {
      if (!sunwayearth.haplochromatization) {
        sunwayearth.haplochromatization = [];
      }
      sunwayearth.haplochromatization.push(czmObject);
    }
    function clear() {
      del();
      sunwayearth.interaction.picking.enabled = true;
      data.form = {
        name: "",
        topHeight: 0,
        botttomHeight: 0,
        color: [1, 1, 0, 0.5],
        textarea: "//p对象即为当前模型\nalert('名称：'+p.name);",
      };
      data.operationEvent = undefined;
      data.editing = undefined;
      data._disposers = [];
      data.selectColor = "rgba(255, 255, 0, 0.5)";
      data._classificationpolygon = undefined;
    }
    function del() {
      sunwayearth.interaction.picking.enabled = false;
      if (data._disposers) {
        data._disposers.forEach((e: any) => e());
        data._disposers.length = 0;
      }
      if (data._classificationpolygon) {
        data._classificationpolygon.destroy();
        data._classificationpolygon = undefined;
      }
    }
    const refData = toRefs(data);
    return {
      ...refData,
      init,
      save,
      clear,
      del,
      creatNewClass,
    };
  },
});
