import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  computed,
  watch,
  defineComponent
} from "vue";
import { useStore } from "vuex";
import { Service } from "~/utils/service";
export default defineComponent({
    name:"EpsplanetWater",
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    const thisVue = proxy;
    const data=reactive({
      creating: false,
      editing: false,
      isSaved: false,
      formRef: "",
      destroyArray:[""],
      form: {
        name: "",
        animationSpeed: 10,
        specularIntensity: 0.5,
        sliderValue3: 2,
        show: true,
        outlineshow: true,
        ground: true,
        amplitude: 2,
      },
    });
    let _waterDraw:any;
    let pin:any;
    const methods={
       init() {
        pin = new XE.Obj.Plots.GeoPin(sunwayearth);
      //console.log(this.$store.state,store.state);
      let waterDraw = new XE.Obj.Water(sunwayearth);
      console.log(waterDraw);
      
      _waterDraw=waterDraw;
      waterDraw.creating = true;
      data.destroyArray = [];
      let position=[0,0,0];
      data.destroyArray.push(
        XE.MVVM.bind(thisVue, "form.name", waterDraw, "name"),
        XE.MVVM.bind(thisVue, "editing", waterDraw, "editing"),
        XE.MVVM.bind(thisVue, "form.show", waterDraw, "show"),
        XE.MVVM.bind(thisVue, "form.outlineshow", waterDraw.outline, "show"),
        XE.MVVM.bind(thisVue, "form.animationSpeed", waterDraw, "animationSpeed"),
        XE.MVVM.bind(thisVue, "form.amplitude", waterDraw, "amplitude"),
        XE.MVVM.bind(
          thisVue,
          "form.specularIntensity",
          waterDraw,
          "specularIntensity"
        ),
        XE.MVVM.bind(thisVue, "form.ground", waterDraw, "ground"),
        XE.MVVM.watch(
            () => [...waterDraw.positions],
            () => {
              waterDraw.positions.forEach((element,index) => {
                position[index]=element;
              });
              
              if (waterDraw.positions.length == 2) {
                pin.position =position;
                pin.innerHTML =
                  '<div style="cursor:pointer;position:absolute;width:300px;left:6px;line-height:15px;color:white;"><span style="padding:2px;border-radius:2px;text-stroke:2px #000;font-size:14px;color:#ffffff;background-color:rgba(50,50,50,0.5)">点击左键绘制水面,按住shift+右键完成绘制</span></div>';
              }  else{
                pin.enabled = false;
              }
            }
          )
      );
      console.log(data.destroyArray);
      
    },
    
    del() {
      pin.enabled = false;
      if (data.destroyArray) {
        data.destroyArray.forEach((d:any) => d());
        data.destroyArray.length = 0;
      }
      if (_waterDraw && !data.isSaved) 
      _waterDraw.destroy();
      _waterDraw = undefined;
    },
    save() {
      data.isSaved = true;
      const sceneObject = new XE.SceneTree.Leaf(_waterDraw);
      console.log(_waterDraw);
      sunwayearth.sceneTree.root.children.push(sceneObject);
      // thisVue.confirm(sceneObject);
    }};
     onMounted(async()=>{
         sunwayearth = await Service.instance.getEarth();
       methods.init();
     });
    onBeforeUnmount(()=>{
      methods.del();
    });
    return{...methods,...toRefs(data)}
  },
  
