
import {
  reactive,
  toRefs,
  onMounted,
  watch,
  onBeforeUnmount,
  defineComponent,
} from "vue";
import { Service } from "~/utils/service";
export default defineComponent({
  name: "EpsplanetCloud",
  props: [],
  setup() {
    let sunwayearth: any;
    const data = reactive({
      title: "环绕周期",
      aroundTime: 120,
      min: 1,
      max: 200,
    });
    watch(
      //  监听数值变化
      () => data.aroundTime,
      (newVal, old) => {
        sunwayearth.weather.cloud.aroundTime = newVal;
      }
    );
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      sunwayearth.weather.cloud.enabled = true;
    });
    onBeforeUnmount(() => {
      sunwayearth.weather.cloud.enabled = false;
    });
    const refData = toRefs(data);
    return {
      ...refData,
    };
  },
});
