<template>
  <div class="epsplanet-panel-base" style="width: 296px">
    <el-form ref="form" label-width="70px">
      <el-form-item :label="title">
        <el-slider
          v-model="gradations"
          :min="min"
          :max="max"
          :step="step"
          show-input
          :show-input-controls="false"
        ></el-slider>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  watch,
  onBeforeUnmount,
} from "vue";
import { Service } from "~/utils/service";
export default {
  name: "EpsplanetBlackwhite",
  props: [],
  setup() {
    const ctx = getCurrentInstance();
    let sunwayearth;
    const data = reactive({
      title: "黑白程度",
      gradations: 5,
      min: 1,
      max: 20,
      step: 0.1,
    });
    watch(
      //  监听数值变化
      () => data.gradations,
      (newVal, old) => {
        sunwayearth.postProcess.blackAndWhite.gradations = newVal;
      }
    );
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      sunwayearth.postProcess.blackAndWhite.enabled = true;
    });
    onBeforeUnmount(() => {
      sunwayearth.postProcess.blackAndWhite.enabled = false;
    });
    const refData = toRefs(data);
    return {
      ...refData,
    };
  },
};
</script>
