
/* eslint-disable */
import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
  ref,
} from "vue";
import { Service } from "~/utils/service";
import _ from "lodash";
import axios from "axios";
import JSZip from "jszip";
import FileSaver from "file-saver";
import { ElMessage } from "element-plus";
export default defineComponent({
  name: "EpsplanetDataOutput",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    let proxyurl: any;
    let polygon: any;
    let polygonstr: string;
    let pin: any;
    let lefthandler: any;
    let righthandler: any;
    let listen: any;
    const thisVue = proxy;
    const data = reactive({
      curlayer: "" as any,
      layerList: [] as any,
      exportType: "shp",
      queryRange: "all",
    });
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      loadconfig();
    });
    onBeforeUnmount(() => {
      del();
    });
    // 加载配置文件
    const loadconfig = async () => {
      await axios
        .get("Epsplanet_Assets/widgets/others/dataOutput/config.json")
        .then((res) => {
          data.layerList = res.data.layerList;
          proxyurl = res.data.proxy;
        })
        .catch((err) => {
          ElMessage.error("加载配置文件失败");
          del();
        });
    };
    //导出数据
    const exportdata = () => {
      if (!polygonstr && data.queryRange == "draw") {
        ElMessage.warning("未识别到范围线信息,");
        return;
      }
      //先根据选中的图层以及格式参数拼凑出url
      let urlarr: any = []; // 需要下载打包的路径, 可以是本地相对路径, 也可以是跨域的全路径

      data.curlayer.forEach((clayer) => {
        data.layerList.forEach((layer) => {
          if (clayer == layer.name) {
            let outputFormat =
              data.exportType == "shp" ? "shape-zip" : "application/json";
            let filterStr =
              data.queryRange == "all"
                ? "1=1"
                : `INTERSECTS(${layer.geometry_name},SRID=4326;POLYGON((${polygonstr})))`;
            let axiosUrl: string = `${proxyurl}/${layer.namespace}/wfs?service=wfs
          &request=GetFeature&version=1.1.0
          &outputFormat=${outputFormat}
          &TYPENAME=${layer.namespace}:${layer.name}
          &CQL_FILTER=${filterStr}`;
            //创建多图层服务地址
            urlarr.push(axiosUrl);
          }
        });
      });
      let length = data.curlayer.length;
      switch (length) {
        case 0:
          console.log("导出失败，未选择图层！！！");
          break;
        case 1:
          output(urlarr[0]);
          break;
        default:
          batchDownload(urlarr);
          break;
      }
    };

    // 绘制范围线
    const drawRangeline = () => {
      //先判断是否存在多边形对象以及提示点信息
      if (!polygon && !pin) {
        //创建兴趣点，用作提示
        pin = new XE.Obj.Plots.GeoPin(sunwayearth);
        pin.innerHTML =
          '<div style="cursor:pointer;position:absolute;width:300px;left:6px;line-height:15px;color:white;"><span style="padding:2px;border-radius:2px;text-stroke:2px #000;font-size:14px;color:#ffffff;background-color:rgba(50,50,50,0.5)">点击左键开始绘制!</span></div>';
        // 创建多边形
        polygon = new XE.Obj.Plots.GeoPolygon(sunwayearth);
        polygon.creating = true;
        // 数据鼠标左击事件
        lefthandler = new Cesium.ScreenSpaceEventHandler(
          sunwayearth.czm.viewer.scene.canvas
        );
        lefthandler.setInputAction((event) => {
          if (polygon.positions.length >= 4) {
            pin.innerHTML =
              '<div style="cursor:pointer;position:absolute;width:300px;left:6px;line-height:15px;color:white;"><span style="padding:2px;border-radius:2px;text-stroke:2px #000;font-size:14px;color:#ffffff;background-color:rgba(50,50,50,0.5)">按"SHITF + 右键"结束绘制!</span></div>';
          }
        }, Cesium.ScreenSpaceEventType.LEFT_CLICK);
        // 数据鼠标右击事件，用以监听多边形绘制结束的操作
        righthandler = new Cesium.ScreenSpaceEventHandler(
          sunwayearth.czm.viewer.scene.canvas
        );
        righthandler.setInputAction(
          (event) => {
            pin.enabled = false;
            polygonstr = getPolygonstr(polygon.positions);
          },
          Cesium.ScreenSpaceEventType.RIGHT_CLICK,
          Cesium.KeyboardEventModifier.SHIFT
        );
        listen = XE.MVVM.watch(
          () => [...polygon.positions],
          () => {
            pin.position = polygon.positions[polygon.positions.length - 1];
          }
        );
        window["polygon"] = polygon;
        window["pin"] = pin;
      } else {
        if (polygon && pin) {
          pin.innerHTML =
            '<div style="cursor:pointer;position:absolute;width:300px;left:6px;line-height:15px;color:white;"><span style="padding:2px;border-radius:2px;text-stroke:2px #000;font-size:14px;color:#ffffff;background-color:rgba(50,50,50,0.5)">点击左键开始绘制!</span></div>';
          polygon.creating = true;
          polygon.show = true;
          pin.enabled = true;
        }
      }
    };
    //经纬度(弧度)转成经纬度(度数)，并且整合成"lon1 lat1,lon2 lat2,lon3 lat3,lon4 lat4,lon1 lat1"的格式并返回
    const getPolygonstr = (positions: any) => {
      let polystr: string = "";
      let laststr: string = "";
      positions.forEach((pos, index) => {
        let lng = Cesium.Math.toDegrees(pos[0]);
        let lat = Cesium.Math.toDegrees(pos[1]);
        polystr += `${lng} ${lat},`;
        laststr = index == 0 ? `${lng} ${lat}` : laststr;
      });
      polystr = polystr + laststr;
      return polystr;
    };

    // 把路径指向文件转成ArrayBuffer对象
    const getFile = (url) => {
      return new Promise((resolve, reject) => {
        axios({
          method: "get",
          url,
          responseType: "arraybuffer",
        })
          .then((data) => {
            resolve(data.data);
          })
          .catch((error) => {
            reject(error.toString());
          });
      });
    };

    //批量图层导出成压缩zip文件(神奇，使用jszip,居然可以把json或者zip文件都添加到新建的zip文件中)
    const batchDownload = (urlarr: any[]) => {
      const zip = new JSZip();
      const cache = {};
      const promises: any = [];
      urlarr.forEach((url, index) => {
        const promise = getFile(url).then((geojson: any) => {
          // 获取文件名。为了避免文件名重复，统一自行命名
          const file_name =
            data.exportType == "geojson"
              ? "导出geojson" + index + ".json"
              : "导出shp" + index + ".zip";

          zip.file(file_name, geojson, { binary: true }); // 逐个添加文件
          cache[file_name] = geojson;
        });
        promises.push(promise);
      });
      Promise.all(promises).then(() => {
        zip.generateAsync({ type: "blob" }).then((content) => {
          let filename =
            data.exportType == "geojson"
              ? "批量下载geojson.zip"
              : "批量下载shp.zip";
          // 生成二进制流
          FileSaver.saveAs(content, filename); // 利用file-saver保存文件
        });
      });
    };

    //单个文件下载（包括geojson和shp）
    const output = async (axiosUrl) => {
      if (data.exportType == "geojson") {
        await axios
          .get(axiosUrl)
          .then((response) => {
            if (response.status == 200 && response.statusText == "OK") {
              let datas = JSON.stringify(response.data);
              const fileName = data.curlayer[0] + ".json";
              // response.headers["content-disposition"].split("=")[1] ||
              const blob = new Blob([datas], { type: "application/json" });
              const href = URL.createObjectURL(blob);
              const a = document.createElement("a");
              document.body.appendChild(a);
              a.setAttribute("href", href);
              a.setAttribute("download", fileName);
              a.click();
              URL.revokeObjectURL(href);
              document.body.removeChild(a);
            }
          })
          .catch((e) => {
            console.log(e);
            // ElMessage.error(e);
          });
      } else if (data.exportType == "shp") {
        const fileName = data.curlayer[0] + ".zip";
        const a = document.createElement("a");
        document.body.appendChild(a);
        a.setAttribute("href", axiosUrl);
        a.setAttribute("download", fileName);
        a.click();
        URL.revokeObjectURL(axiosUrl);
        document.body.removeChild(a);
      }
    };

    const selectRange = (range: string) => {
      if (range == "all") {
        polygon.creating = false;
        polygon.show = false;
        pin.innerHTML = "";
      } else {
        drawRangeline();
      }
    };

    function del() {
      if (polygon) {
        polygon.destroy();
        polygon = undefined;
      }
      //销毁pin对象
      if (pin) {
        pin.destroy();
        pin = undefined;
      }
      //销毁左、右点击事件
      if (lefthandler || righthandler) {
        lefthandler = lefthandler && lefthandler.destroy();
        righthandler = righthandler && righthandler.destroy();
      }
      //取消监听
      listen();
    }
    const refData = toRefs(data);
    return {
      ...refData,
      del,
      selectRange,
      exportdata,
    };
  },
});
