
/* eslint-disable */
import {
  reactive,
  toRefs,
  defineComponent,
  getCurrentInstance,
  onMounted,
  onBeforeUnmount,
  watch,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetSurfaceOpacity",
  props: [],
  setup() {
    let sunwayEarth: any;
    const { proxy } = getCurrentInstance() as any;
    const thisVue = proxy;
    const data = reactive({
      _disposer: [] as any,
      // subSurfaceEnabled: false, // 是否开启地下模式
      surfaceOpacity: 0.0 as number, // 地表透明度
    });

    onMounted(async () => {
      sunwayEarth = await Service.instance.getEarth();
      init();
    });
    onBeforeUnmount(() => {
      del();
    });
    // watch(
    //   //  监听数值变化
    //   () => data.surfaceOpacity,
    //   (newVal, old) => {
    //     sunwayEarth.terrainEffect.surfaceOpacity = newVal;
    //   }
    // );
    function init() {
      data._disposer.push(
        // XE.MVVM.bind(
        //   thisVue,
        //   "subSurfaceEnabled",
        //   sunwayEarth.terrainEffect,
        //   "subSurfaceEnabled"
        // ),
        XE.MVVM.bind(
          thisVue,
          "surfaceOpacity",
          sunwayEarth.terrainEffect,
          "surfaceOpacity"
        ),
        // XE.MVVM.watch(thisVue, "surfaceOpacity", (opacity: number) => {
        //   sunwayEarth.sceneTree.root.children.forEach((item: any) => {
        //     if (item.czmObject) {
        //       item.czmObject.xbsjStyle = 
        //       "var style={color:\"vec4(1,1,1," + opacity.toString() + ")\"}"
        //     }
        //   })
        // })
      );
    }
    // function formatTooltip(val: any) {
    //   return val / 100
    // }
    function del() {
      data._disposer.forEach((d: any) => d());
      data._disposer.length = 0;
    }
    return {
      ...toRefs(data),
      init,
      // formatTooltip,
      del,
    };
  },
});
