
/* eslint-disable */
import { reactive, toRefs, onBeforeMount, onMounted, getCurrentInstance, onBeforeUnmount, defineComponent, computed, watch } from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetPolyline",
  props: [],
  setup() {
    let sunwayearth: any;
    const { proxy } = getCurrentInstance() as any;
    const thisVue = proxy;
    const data = reactive({
      name:"未命名折线",
      creating: false,
      editing: false,
      width: 1,
      color: "rgba(0,255,0,1)",
      ground: true,
      _disposer: [0],
      isSaved: false,
    });
    let _polylineCzm: any;
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      init();
    });
    onBeforeUnmount(() => {
      del();
    });
    function init() {
      let polylineCzm = new XE.Obj.Plots.GeoPolyline(sunwayearth);
      _polylineCzm = polylineCzm;
      data._disposer = [];
      data._disposer.push(
        XE.MVVM.bind(thisVue, "name", polylineCzm, "name"),
        XE.MVVM.bind(thisVue, "creating", polylineCzm, "creating"),
        XE.MVVM.bind(thisVue, "editing", polylineCzm, "editing"),
        XE.MVVM.bind(thisVue, "width", polylineCzm, "width"),
        XE.MVVM.bind(thisVue, "ground", polylineCzm, "ground"),
        XE.MVVM.watch(
          () => polylineCzm.creating,
          () => {
            data.creating = polylineCzm.creating;
          }
        ),
        XE.MVVM.watch(
          () => polylineCzm.editing,
          () => {
            data.editing = polylineCzm.editing;
          }
        )
      );
    }
    function changeColor(val: any) {
      let v = val
        .split("(")[1]
        .split(")")[0]
        .split(",");
      let cc: any = [];
      v.forEach((c: any, i: number) => {
        if (i == 3) {
          cc.push(Number(c));
        } else {
          cc.push(Number(c) / 255.0);
        }
      });
      if (_polylineCzm) {
        _polylineCzm.color = cc;
      }
    }
    function save() {
      data.isSaved = true;
      const sceneObject = new XE.SceneTree.Leaf(_polylineCzm);
      sunwayearth.sceneTree.root.children.push(sceneObject);
      // thisVue.confirm(sceneObject);
    }
    function del() {
      if (_polylineCzm && !data.isSaved) {
        _polylineCzm.destroy();
        _polylineCzm = undefined;
      }
      if (data._disposer) {
        data._disposer.forEach((d: any) => d());
        data._disposer.length = 0;
      }
    }
    const refData = toRefs(data);
    return {
      ...refData,
      init,
      changeColor,
      save,
      del,
    };
  },
});
