
import { onMounted, defineComponent } from "vue";
import { Service } from "~/utils/service";
import { changeBtnState } from "~/utils/domTool";

export default defineComponent({
  name: "EpsplanetMeasureClear",
  props: ["btnClass"],
  setup(props) {
    let sunwayEarth: any;
    onMounted(async () => {
      sunwayEarth = await Service.instance.getEarth();
      sunwayEarth.analyzation.measurement.clearResults();
      if (sunwayEarth.analyzation.measurement.clearThreeDArea) {
        sunwayEarth.analyzation.measurement.clearThreeDArea();
      }
      if (sunwayEarth.analyzation.measurement.clearGroundDistance) {
        sunwayEarth.analyzation.measurement.clearGroundDistance();
      }
      if (sunwayEarth.analyzation.measurement.clearAzimuth) {
        sunwayEarth.analyzation.measurement.clearAzimuth();
      }
      sunwayEarth.terrainEffect.depthTest = false;
      changeBtnState(props.btnClass, false);
    });
  },
});
