
/* eslint-disable */
import {
  defineComponent,
  onMounted,
  onBeforeUnmount,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetUnderground",
  props: [],
  setup() {
    let sunwayEarth: any;
    onMounted(async () => {
      sunwayEarth = await Service.instance.getEarth();
      sunwayEarth.terrainEffect.subSurfaceEnabled = true;
    });
    onBeforeUnmount(() => {
      sunwayEarth.terrainEffect.subSurfaceEnabled = true;
    });
  },
});
