
/* eslint-disable */
import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
  watch,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetContour",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    const thisVue = proxy;
    const data = reactive({
      label: {
        width: "线宽",
        spacing: "间距",
        color: "颜色",
      },
      width: 1,
      spacing: 100,
      color: [],
      uicolor: "rgba(249, 42, 0, 1)",
    });
    let _disposer: any = [];
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      init();
    });
    onBeforeUnmount(() => {
      del();
    });

    function init() {
      sunwayearth.terrainEffect.contour.enabled = true;
      _disposer.push(
        XE.MVVM.bind(
          thisVue,
          "width",
          sunwayearth.terrainEffect.contour,
          "width"
        ),
        XE.MVVM.bind(
          thisVue,
          "spacing",
          sunwayearth.terrainEffect.contour,
          "spacing"
        )
      );
    }

    function changeColor(val: string) {
      let v = val.split("(")[1].split(")")[0].split(",");
      let cc: any = [];
      v.forEach((c: any, i: number) => {
        if (i == 3) {
          cc.push(Number(c));
        } else {
          cc.push(Number(c) / 255.0);
        }
      });
      sunwayearth.terrainEffect.contour.color = cc;
    }
    function del() {
      sunwayearth.terrainEffect.contour.enabled = false;
      if (_disposer) {
        _disposer.forEach((d: any) => d());
        _disposer.length = 0;
      }
    }
    const refData = toRefs(data);
    return {
      ...refData,
      init,
      changeColor,
      del,
    };
  },
});
