
/* eslint-disable */
import getExtremum from "sdk/XbsjEarth-Plugins/getExtremum";

import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
  watch,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetSliced",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    const thisVue = proxy;
    let sunwayearth: any;
    const data = reactive({
      name: "剖切面对象", //剖切面对象名称
      section: true, //纵剖 false 为横剖
      rotation: [0, 0, 0], //姿态
      ifShow: true, //是否显示辅助面
      linked: true, //是否关联
      positionEditing: false,
      rotationEditing: false,
      tilesetOptions: [{}], // 场景树种的tileset类型数据
      selectScene: undefined, //选中的场景
    });
    let clippingPlane: any;
    let tiletest: any;
    let _disposer: any = [];
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      if (!clippingPlane) {
        //试验数据，大雁塔倾斜模型，tileset格式
        methods.addtestdata();
      }
    });
    onBeforeUnmount(() => {
      methods.del();
    });
    const methods = {
      init() {
        //创建剖切面对象
        clippingPlane = new XE.Obj.ClippingPlane(sunwayearth);
        console.log(clippingPlane);
        clippingPlane.name = thisVue.name;
        clippingPlane.position = [
          1.9016974701882112, 0.5972325152147303, 425.8641913624607,
          // (124.461089 / 180) * Math.PI,
          // (40.444334 / 180) * Math.PI,
          // 0,
        ];
        //仅用于调试
        window["clippingPlane"] = clippingPlane;

        _disposer = [];
        _disposer.push(
          XE.MVVM.bind(
            thisVue,
            "positionEditing",
            clippingPlane,
            "positionEditing"
          ),
          XE.MVVM.bind(
            thisVue,
            "rotationEditing",
            clippingPlane,
            "rotationEditing"
          ),
          XE.MVVM.bind(thisVue, "ifShow", clippingPlane, "show"),
          XE.MVVM.bindRotation(thisVue, "rotation", clippingPlane, "rotation")
        );
      },
      addtestdata() {
        //试验数据，大雁塔倾斜模型，tileset格式
        let model = {
          ref: "tileset",
          czmObject: {
            xbsjType: "Tileset",
            xbsjGuid: "b4137394-d346-4434-a01e-5b2d3730ec9f",
            name: "大雁塔模型",
            url: "//lab.earthsdk.com/model/f15b9e90ac2d11e99dbd8fd044883638/tileset.json",
            xbsjPosition: [
              1.9016974701882112, 0.5972325152147303, 425.8641913624607,
            ],
            xbsjUseOriginTransform: false, //xbsjUseOriginTransform: false的情况下，xbsjPosition才有效
            // xbsjClippingPlanes: {
            //   planes: [clippingPlane.xbsjGuid],
            // },
          },
        };
        let model2 = {
          ref: "test2",
          czmObject: {
            name: "南石槽子桥",
            xbsjType: "Tileset",
            xbsjGuid: "b4137394-d346-4434-a01e-5b2d3730ec9a",
            url: "http://jojo1986.f3322.net:2020/data/3dtiles/%E5%8D%97%E7%9F%B3%E6%A7%BD%E5%AD%90%E6%A1%A5/tileset.json",
            xbsjUseOriginTransform: false, //xbsjUseOriginTransform: false的情况下，xbsjPosition才有效
            xbsjPosition: [
              (124.461089 / 180) * Math.PI,
              (40.444334 / 180) * Math.PI,
              0,
            ],
            // xbsjClippingPlanes: {
            //   planes: [clippingPlane.xbsjGuid],
            // },
          },
        };
        let model3 = {
          ref: "test3",
          czmObject: {
            name: "倾斜模型测试1",
            xbsjType: "Tileset",
            xbsjGuid: "b4137394-d346-4434-a01e-5b2d3730ec9a",
            url: "http://39.152.31.83:11215/epsplanet/redirect/440a7e9564ea46809d6c0471cf25b84f/文件1/18ccc169ee2d4c79997ec65005abb32d/tileset.json?token=BbNgktJDJkqE2Sj1Iivy0gjQwLIlZAfN",
            // xbsjUseOriginTransform: false, //xbsjUseOriginTransform: false的情况下，xbsjPosition才有效
            // xbsjPosition: [
            //   (124.461089 / 180) * Math.PI,
            //   (40.444334 / 180) * Math.PI,
            //   0,
            // ],
          },
        };
        // sunwayearth.sceneTree.root.children.push(model3);
        // tiletest = sunwayearth.sceneTree.$refs.test3.czmObject;
        // tiletest.flyTo();
      },

      changeTileset(val) {
        debugger
        if (!clippingPlane) {
          methods.init();
        }
        sunwayearth.sceneTree.root.children.forEach((node) => {
          if (node.czmObject.xbsjGuid == val) {
            node.czmObject.flyTo();
            node.czmObject.show = true;
            node.czmObject.xbsjClippingPlanes = {
              planes: [clippingPlane.xbsjGuid],
            };
            // clippingPlane.position = node.czmObject.xbsjPosition;
          }
        });
      },
      //下拉框出现时读取场景树
      readScenetree(val) {
        if (val) {
          data.tilesetOptions = [];
          sunwayearth.sceneTree.root.children.forEach((node) => {
            if (node.czmObject.xbsjType == "Tileset") {
              let json = {
                value: node.czmObject.xbsjGuid,
                label: node.czmObject.name,
              };
              data.tilesetOptions.push(json);
            }
          });
        }
      },
      SectionMethod() {
        if (thisVue.section) {
          thisVue.rotation[1] = 0;
        } else {
          thisVue.rotation[1] = 90;
        }
      },
      del() {
        if (clippingPlane) {
          clippingPlane.destroy();
          clippingPlane = undefined;
        }
        if (tiletest) {
          sunwayearth.sceneTree.root.children.forEach((node) => {
            if (node.czmObject.xbsjGuid == tiletest.xbsjGuid) {
              sunwayearth.sceneTree.root.children.remove(node);
            }
          });
        }
        if (_disposer) {
          _disposer.forEach((d: any) => d());
          _disposer.length = 0;
        }
      },
    };
    const refData = toRefs(data);
    return {
      ...refData,
      ...methods,
    };
  },
});
