
/* eslint-disable */
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
  computed,
  watch,
} from "vue";
import { Service } from "~/utils/service";
// import publicFunc from "../../mixins/methods";
export default defineComponent({
  name: "EpsplanetSmoothPolygon",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    const thisVue = proxy;
    const data = reactive({
      name: "平滑多边形对象", //多边形名称
      outlineWidth: 2, //边框线的宽度，单位m
      outlineShow: true, //有无边框
      wireFrameColor: "rgba(0,255,0,1)", //边框颜色
      fillColor: "rgba(0,255,0,1)", //填充颜色
      editing: false, //是否启用编辑
      ground: true, //是否贴地
    });
    let sunwayearth: any;
    let creating: false; //是否创建多边形
    let SmoothPolygon: any; //平滑多边形对象
    let isSaved: any = false; //是否挂载到sceneTree.root下
    let bindList: any;
    let pin: any; //兴趣点
    let lefthandler: any; //鼠标左击事件
    let righthandler: any; //鼠标右击事件
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      if (!SmoothPolygon) {
        init();
      }
    });
    onBeforeUnmount(() => {
      del();
    });
    const init = () => {
      //创建兴趣点，用作提示
      pin = new XE.Obj.Plots.GeoPin(sunwayearth);
      pin.innerHTML =
        '<div style="cursor:pointer;position:absolute;width:300px;left:6px;line-height:15px;color:white;"><span style="padding:2px;border-radius:2px;text-stroke:2px #000;font-size:14px;color:#ffffff;background-color:rgba(50,50,50,0.5)">点击左键开始绘制!</span></div>';
      // this.pin.creating = true;
      // 创建粒子系统
      SmoothPolygon = new XE.Obj.Plots.GeoSmoothPolygon(sunwayearth);
      window["SmoothPolygon"] = SmoothPolygon;
      window["pin"] = pin;
      SmoothPolygon.name = data.name;
      SmoothPolygon.ground = data.ground;
      SmoothPolygon.outlineWidth = data.outlineWidth;
      SmoothPolygon.outlineShow = data.outlineShow;
      SmoothPolygon.creating = true;
      // 数据鼠标左击事件
      lefthandler = new Cesium.ScreenSpaceEventHandler(
        sunwayearth.czm.viewer.scene.canvas
      );
      lefthandler.setInputAction((event) => {
        if (SmoothPolygon.positions.length >= 3) {
          pin.innerHTML =
            '<div style="cursor:pointer;position:absolute;width:300px;left:6px;line-height:15px;color:white;"><span style="padding:2px;border-radius:2px;text-stroke:2px #000;font-size:14px;color:#ffffff;background-color:rgba(50,50,50,0.5)">按"SHITF + 右键"结束绘制!</span></div>';
        }
      }, Cesium.ScreenSpaceEventType.LEFT_CLICK);
      // 数据鼠标右击事件，用以监听平滑多边形绘制结束的操作
      righthandler = new Cesium.ScreenSpaceEventHandler(
        sunwayearth.czm.viewer.scene.canvas
      );
      righthandler.setInputAction(
        (event) => {
          pin.enabled = false;
        },
        Cesium.ScreenSpaceEventType.RIGHT_CLICK,
        Cesium.KeyboardEventModifier.SHIFT
      );
      // 数据绑定
      bindList = [
        //双向绑定的属性
        { name: "name" },
        { name: "creating" },
        { name: "editing" },
        { name: "ground" },
        { name: "outlineWidth" },
        { name: "outlineShow" },
      ];
      bindList.forEach((el: any) => {
        el.unbind = XE.MVVM.bind(thisVue, el.name, SmoothPolygon, el.name);
      });
      let el: any = { name: "position" };
      el.unbind = XE.MVVM.watch(
        () => [...SmoothPolygon.positions],
        () => {
          pin.position =
            SmoothPolygon.positions[SmoothPolygon.positions.length - 1];
        }
      );
      bindList.push(el);
    };
    // 改变颜色
    const changeColor = (val) => {
      let cc = [];
      if (val == "box") {
        cc = cc.concat(transColorToCesium(data.wireFrameColor));
        SmoothPolygon.outlineColor = cc;
      }
      if (val == "fill") {
        cc = cc.concat(transColorToCesium(data.fillColor));
        SmoothPolygon.color = cc;
      }
    };
    // 把rgba颜色转换为cesium通用的颜色格式（0，1）
    const transColorToCesium = (val: any) => {
      let v = val.split("(")[1].split(")")[0].split(",");
      let cc: any = [];
      v.forEach((c: any, i: number) => {
        if (i == 3) {
          cc.push(Number(c));
        } else {
          cc.push(Number(c) / 255.0);
        }
      });
      return cc;
    };
    const del = () => {
      //解除与平滑多边形的属性绑定
      if (bindList) {
        bindList.forEach((el: any) => {
          el.unbind();
        });
        bindList.length = 0;
      }
      //销毁平滑多边形对象
      if (SmoothPolygon && !isSaved) {
        SmoothPolygon.destroy();
        SmoothPolygon = undefined;
      }
      //销毁pin对象
      if (pin) {
        pin.destroy();
        pin = undefined;
      }
      //销毁左、右点击事件
      if (lefthandler || righthandler) {
        lefthandler = lefthandler && lefthandler.destroy();
        righthandler = righthandler && righthandler.destroy();
      }
    };
    const save = () => {
      SmoothPolygon.editing = false;
      isSaved = true;
      const sceneObject = new XE.SceneTree.Leaf(SmoothPolygon);
      sunwayearth.sceneTree.root.children.push(sceneObject);
      // thisVue.confirm(sceneObject);
    };
    const refData = toRefs(data);
    return {
      ...refData,
      init,
      changeColor,
      transColorToCesium,
      save,
      del,
    };
  },
});
