
import { defineComponent, onBeforeUnmount, onMounted, reactive, toRefs, watch } from "vue";
import { Service } from "~/utils/service";
export default defineComponent({
  name: "EpsplanetSnow",
  setup() {
    const data = reactive({
      title: "积雪程度",
      alpha: 0.6,
      min: 0,
      max: 1,
      step: 0.1,
      emissionRate: 0.5,
    });
    let sunwayearth: any;
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      sunwayearth.weather.snowPostProcess.enabled = true;
    });
    onBeforeUnmount(() => {
      sunwayearth.weather.snowPostProcess.enabled = false;
    });
    watch(
      () => data.alpha,
      (newVal, old) => {
        sunwayearth.weather.snowPostProcess.alpha = newVal;
      }
    );
    return {
      ...toRefs(data),
    };
  },
});
