
/* eslint-disable */
import axios from "axios";
import { ElMessage } from "element-plus";
import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
  watch,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetAviation",
  props: [],
  setup() {
    let sunwayEarth: any;
    const { proxy } = getCurrentInstance() as any;
    const thisVue = proxy;
    const data = reactive({
      // _pathCzm: undefined, // 当前路径
      // _modelCzm: undefined, // 飞行器模型
      _disposer: [0], // 用于解除双向绑定的数组
      modelDisposer: [0], // 解除模型绑定数组
      ellipsoid: undefined, // 椭球体
      creating: false, // 是否处于创建模式
      editing: false, // 是否处于编辑模式
      currentSpeed: 50, // 当前播放速度，单位为米/秒
      showPath: true, // 是否显示路径
      playing: false, // 当前是否播放
      modelUrl: "", // 模型数据
      modelList: [] as any, // 模型列表
      currentView: "",
      viewOptions: [
        {
          label: "第一视角",
          value: "firstPersonView",
        },
        {
          label: "跟随视角",
          value: "attachedView",
        },
      ],
      showOffset: true, // 是否显示偏移面板
      isSliderDisabled: false, // 是否禁用滑块
      viewD: 200, // 视角距离：默认值200、最小值0、最大值1000.
      viewHeight: 50, // 视角高度：当前相机高度、默认值50、最小值0、最大值1000.
      xOffset: 0, // 前后偏移：默认值0、最小值0、最大值1000.
      yOffset: 0, // 横向偏移：默认值0、最小值0、最大值1000.
      zOffset: 0, // 高度偏移：默认值0、最小值0、最大值1000.
      currentPosition: [], // 当前模型位置
      offsetPosition: [], // 相机偏移位置
      cameraPosition: [], // 当前相机位置
    });
    let _pathCzm: any, _modelCzm: any;
    onMounted(async () => {
      sunwayEarth = await Service.instance.getEarth();
      axios.get("Epsplanet_Assets/widgets/path/aviation/modelList.json")
      .then(res => {
        data.modelList = res.data.modelList
      })
      .catch(err => {
        ElMessage("加载配置文件失败")
        del()
      })
      if (!_pathCzm) {
        init();
      } else {
        del();
      }
    });
    onBeforeUnmount(() => {
      del();
    });
    watch(thisVue.playing, () => {
      if (!thisVue.playing && thisVue.cameraAttached) {
        _modelCzm.cameraAttached = false;
      }
    });
    function init() {
      data._disposer = [];
      data.modelDisposer = [];
      // data.modelList = [];
      thisVue.ellipsoid = sunwayEarth.czm.scene.globe.ellipsoid;
      let path = new XE.Obj.Path(sunwayEarth);
      path.alwaysAlongThePath = true;
      _pathCzm = path;
      // 双向绑定的属性
      data._disposer.push(
        XE.MVVM.bind(thisVue, "showPath", path, "show"),
        XE.MVVM.bind(thisVue, "currentSpeed", path, "currentSpeed"),
        XE.MVVM.bind(thisVue, "currentPosition", path, "currentPosition"),
        XE.MVVM.bind(thisVue, "playing", path, "playing"),
        XE.MVVM.bind(thisVue, "creating", path, "creating"),
        XE.MVVM.bind(thisVue, "editing", path, "editing"),
        XE.MVVM.bind(thisVue, "cameraPosition", sunwayEarth.camera, "position")
      );
    }

    // 获取模型
    // function getModelList() {
    //   if (sunwayEarth.modelCollection && data.modelList.length === 0) {
    //     sunwayEarth.modelCollection.forEach((m: any) => {
    //       let obj: any = {
    //         label: m.czmObject.name,
    //         value: m.czmObject.xbsjGuid,
    //       };
    //       data.modelList.push(obj);
    //     });
    //   }
    // }

    // 改变视角高度
    function viewHeightChange() {
      thisVue.cameraPosition[2] =
        thisVue.currentPosition[2] + thisVue.viewHeight;
    }

    // 改变视角距离
    function viewDChange(currentPosition: any, cameraPosition: any, distance: any) {
      let currentCartesian = Cesium.Cartesian3.fromRadians(
        currentPosition[0],
        currentPosition[1],
        currentPosition[2],
        thisVue.ellipsoid
      );
      let cameraCartesian = Cesium.Cartesian3.fromRadians(
        cameraPosition[0],
        cameraPosition[1],
        cameraPosition[2],
        thisVue.ellipsoid
      );
      let direction = Cesium.Cartesian3.normalize(
        Cesium.Cartesian3.subtract(
          cameraCartesian,
          currentCartesian,
          new Cesium.Cartesian3()
        ),
        new Cesium.Cartesian3()
      );
      let scalar = Cesium.Cartesian3.multiplyByScalar(
        direction,
        distance,
        new Cesium.Cartesian3()
      );
      let newCameraPos = Cesium.Cartographic.fromCartesian(
        Cesium.Cartesian3.add(currentCartesian, scalar, new Cesium.Cartesian3())
      );
      sunwayEarth.camera.position = [
        newCameraPos.longitude,
        newCameraPos.latitude,
        newCameraPos.height,
      ];
    }

    // 更改视角
    function viewTypeChange() {
      _modelCzm.cameraAttached = true;
      thisVue.viewD = 50;
      thisVue.viewHeight = 200;
      thisVue.xOffset = 0;
      thisVue.yOffset = 0;
      thisVue.zOffset = 0;
      if (thisVue.currentView === "firstPersonView") {
        thisVue.showOffset = true;
        thisVue.isSliderDisabled = true;
        thisVue.cameraPosition = [
          _modelCzm.xbsjPosition[0],
          _modelCzm.xbsjPosition[1],
          _modelCzm.xbsjPosition[2] + 30,
        ];
        _modelCzm.cameraAttached = true;
        _modelCzm.cameraAttachedOffsetPosition = [0, 0, 30];
      } else if (thisVue.currentView === "attachedView") {
        thisVue.showOffset = false;
        thisVue.isSliderDisabled = false;
      }
    }

    // 计算偏移位置
    function calculateOffset(xVal: any, yVal: any, zVal: any) {
      // // 首先将当前位置转换为笛卡尔坐标
      // let ellipsoid = earth.czm.scene.globe.ellipsoid
      // let curCartesian = new Cesium.Cartesian3.fromRadians(
      //   thisVue.currentPosition[0], thisVue.currentPosition[1], thisVue.currentPosition[2], ellipsoid
      // )
      // console.log("cur: ", curCartesian)
      // // 转换后的坐标加上偏移量，得到偏移后坐标
      // let newPos = new Cesium.Cartesian3(curCartesian.x + xVal, curCartesian.y + yVal, curCartesian.z + zVal)
      // let newRad = ellipsoid.cartesianToCartographic(newPos);
      // console.log("newRad: ", newRad)
      _modelCzm.cameraAttachedOffsetPosition = [
        xVal / 6371004,
        yVal / 6371004,
        zVal,
      ];
    }
    
    // 绑定模型与路径
    function bindModel(modelUrl) {
      clearModel();
      // sunwayEarth.modelCollection.forEach((item: any) => {
      //   if (item.czmObject.xbsjGuid === id) {
      //     _modelCzm = item.czmObject;
      //   }
      // });
      _modelCzm = new XE.Obj.Model(sunwayEarth);
      _modelCzm.url = modelUrl
      _modelCzm.xbsjPosition = _pathCzm.currentPosition;
      // const sceneObject = new XE.SceneTree.Leaf(_modelCzm);
      // sunwayEarth.sceneTree.root.children.push(sceneObject)
      data.modelDisposer.push(
        XE.MVVM.watch(_pathCzm, "currentPosition", (position: any) => {
          let pos = [...position];
          _modelCzm.xbsjPosition = [pos[0], pos[1], pos[2] + 0.1];
        }),
        XE.MVVM.watch(_pathCzm, "currentRotation", (rotation: any) => {
          let cp = [...rotation];
          _modelCzm.xbsjRotation = [cp[0] - Math.PI * 0.5, cp[1], cp[2]];
        })
      );
    }
    // 清除路径
    function clearPath() {
      if (_pathCzm) {
        _pathCzm = _pathCzm && _pathCzm.destroy();
      }
    }
    // 清除模型绑定
    function clearModel() {
      if (data.modelDisposer.length) {
        data.modelDisposer.forEach((d: any) => d());
        data.modelDisposer.length = 0;
      }
      _modelCzm = _modelCzm && _modelCzm.destroy();
    }
    function del() {
      if (data._disposer.length) {
        data._disposer.forEach((d: any) => d());
        data._disposer.length = 0;
      }
      clearModel();
      clearPath();
    }
    return {
      ...toRefs(data),
      viewHeightChange,
      viewDChange,
      viewTypeChange,
      calculateOffset,
      bindModel,
      clearPath,
    };
  },
});
