
/* eslint-disable */
import {
  defineComponent,
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
} from "vue";
import axios from "axios";
import SparkMD5 from "spark-md5";
import { Service } from "~/utils/service";
import { ElMessage } from "element-plus";
// 引入将json和kml格式转换为树结构的方法
import { geoJSONToTreeData, kmlToTreeData } from "~/utils/convertToTreeData";

export default defineComponent({
  name: "EpsplanetImportData",
  props: [],
  setup() {
    let sunwayEarth: any;
    const { proxy }: any = getCurrentInstance();
    const thisVue = proxy;
    const data = reactive({
      activeMainTab: "1",
      activeDialogTab: "1",
      currentTitle: "",
      currentType: "",
      dialogVisible: false,
      isUpload: false,
      objToLoad: {
        name: "obj",
        url: "",
      },
      treeData: [] as any,
      treeProps: {
        label: "label",
        children: "children",
      },
      infoData: [] as any,
      fileList: [],
    });
    let shapefile = require("shapefile");
    let request: any;
    onMounted(async () => {
      sunwayEarth = await Service.instance.getEarth();
      await axios
        .get("Epsplanet_Assets/widgets/others/importData/requestConfig.json")
        .then((res) => {
          request = res.data.request;
        })
        .catch((err) => {
          ElMessage.error("加载配置文件失败");
        });
    });
    // 根据文件类型选择对应的方法进行加载
    function startLoadFromURL(currentType: any) {
      switch (currentType) {
        case "GeoJSON":
          loadGeoJSONFromURL(data.objToLoad.url);
          break;
        case "kml":
          loadKmlFromURL(data.objToLoad.url);
          break;
        case "gml":
          loadKmlFromURL(data.objToLoad.url);
          break;
        case "shp":
          loadShpFromURL(data.objToLoad.url);
          break;
        default:
          ElMessage.error("文件类型不支持");
          break;
      }
    }
    // 选择本地文件上传
    function startLoadFromLocal() {
      data.isUpload = true;
      // console.log(data.fileList)
      // const file = thisVue.$refs.upload1.uploadFiles.pop().raw;
      const file = thisVue.fileList[0].raw;
      let formData = new FormData();
      formData.append("file", file);
      let fileReader = new FileReader();
      let spark = new SparkMD5.ArrayBuffer();
      //获取文件二进制数据
      fileReader.readAsArrayBuffer(file);
      fileReader.onload = (e: any) => {
        spark.append(e.target.result);
        let md5 = spark.end(); // 计算文件md5码
        console.log(md5);
        formData.append("md5", md5);
        formData.append("name", file.name);
        // 上传文件，获取id
        axios
          .post(request.fileSource, formData, request.formDataConfig)
          .then((res: any) => {
            data.isUpload = false;
            if (res.status === 200 && res.data.success === true) {
              ElMessage.success("上传成功");
              handleSuccess(res, file);
            } else {
              ElMessage.error(res.data.msg);
              addTableRow(file.name, "-", res.data.msg);
            }
          })
          .catch((err) => {
            ElMessage.error(err.msg);
            // console.log(err)
          });
      };
    }

    // 上传成功后，根据id读取文件内容，再根据文件类型进行处理
    function handleSuccess(response: any, file: any) {
      let id = response.data.msg; // 文件在后台的id
      let fileName = file.name.substring(0, file.name.lastIndexOf(".")); // 获取文件名
      let extName = file.name.substring(file.name.lastIndexOf(".") + 1); // 获取文件扩展名
      
      // shp压缩文件处理
      if (extName === "zip") {
        axios.get(request.getShpId + id).then((response: any) => {
          response.data.data.forEach((shpId: any) => {
            let url = request.shpToGeoJSON + shpId;
            axios.get(url).then((res: any) => {
              let obj: any = {
                label: fileName,
                children: geoJSONToTreeData(res),
              };
              data.treeData.push(obj);
              addToScene("xbsjGeoJSON", url); // 显示图形
              addTableRow(fileName, id, "上传成功"); // 显示上传信息
            });
          });
          
        });
      } else {
        // 其他格式文件处理
        let url = request.fileSource + id;
        axios.get(url).then((resFile: any) => {
          // 此处响应值为文件内容
          console.log(resFile);
          // json文件处理
          if (extName === "json") {
            let name = fileName.substring(0, fileName.indexOf("."));
            let obj: any = {
              label: name,
              children: geoJSONToTreeData(resFile.data),
            };
            data.treeData.push(obj);
            addToScene("xbsjGeoJSON", url); // 显示图形
            addTableRow(fileName, id, "上传成功"); // 显示上传信息
          } else if (
            // kml和gml处理
            extName === "kml" ||
            extName === "gml"
          ) {
            let obj: any = {
              label: data.objToLoad.name,
              children: kmlToTreeData(resFile),
            };
            data.treeData.push(obj);
            let fileType = extName === "gml" ? "xbsjGML" : "xbsjKML";
            addToScene(fileType, url); // 显示图形
            addTableRow(fileName, id, "上传成功"); // 显示上传信息
          } else {
            // ElMessage.error("格式错误");
            axios.delete(request.fileSource + id).then((res) => {
              ElMessage.error("格式错误，请重新上传");
            });
          }
        });
        // .then(() => {
        //   return axios.delete(request.fileSource + id)
        // }).then((resDel: any) => {
        //   if (resDel.success === true) {
        //     console.log("删除成功");
        //   } else {
        //     ElMessage.warning(resDel.msg)
        //   }
        // }).catch((err) => {
        //   ElMessage.error(err);
        // });
      }
      
    }
    // 加载在线GeoJSON
    function loadGeoJSONFromURL(url: string) {
      axios.get(url).then((response) => {
        let jsonObj = response.data;
        let obj: any = {
          label: data.objToLoad.name,
          children: geoJSONToTreeData(jsonObj),
        };
        data.treeData.push(obj);
        console.log("tree: ", data.treeData);
        addToScene("xbsjGeoJSON", url);
      });
    }
    // 加载在线kml
    function loadKmlFromURL(url: string) {
      let kmlDoc: any;
      try {
        //IE浏览器
        kmlDoc = new ActiveXObject("Microsoft.xmlDOM");
      } catch (e) {
        //firefox,opera...火狐、欧朋等浏览器
        kmlDoc = document.implementation.createDocument("", "", null);
      }
      try {
        kmlDoc.async = false; //是否异步调用
        kmlDoc.load(url); //文件路径
      } catch (e) {
        //chrome
        let xmlHttp: any = new window.XMLHttpRequest();
        //创建一个新的http请求，并指定此请求的方法、URL以及验证信息
        xmlHttp.open("GET", url, false);
        xmlHttp.send(null);
        if (xmlHttp.readyState === 4) {
          if (xmlHttp.responseXML) {
            kmlDoc = xmlHttp.responseXML.documentElement;
          } else {
            ElMessage.error("文件格式错误");
          }
        } else {
          ElMessage.error("导入失败，未找到指定文件");
        }
      }
      // 构造树组件
      let obj: any = {
        label: data.objToLoad.name,
        children: kmlToTreeData(kmlDoc),
      };
      data.treeData.push(obj);
      let fileType = url.indexOf(".kml") === -1 ? "xbsjGML" : "xbsjKML";
      addToScene(fileType, url);
    }
    // 加载在线shp（转换成geojson格式）
    function loadShpFromURL(url: string) {
      shapefile
        .open(url)
        .then((source: any) => {
          source.read().then(res => {
            console.log(res);
          });
        })
    }

    function addTableRow(fileName, id, status) {
      let rowObj = {
        fileName: fileName,
        id: id,
        status: status,
      };
      data.infoData.push(rowObj);
    }

    // 在场景中显示对象（未保存到场景树）
    function addToScene(type: string, url: string) {
      let obj = {
        ref: url.slice(-32),
        name: data.objToLoad.name,
        url: url,
        xbsjType: type,
      };
      // 根据类型构造对象
      if (type === "xbsjGeoJSON") {
        let json = new XE.Obj.GeoJSON(sunwayEarth);
        json.xbsjFromJSON(obj);
      } else if (type === "xbsjKML") {
        let kml = new XE.Obj.KML(sunwayEarth);
        kml.xbsjFromJSON(obj);
      } else {
        let gml = new XE.Obj.GML(sunwayEarth);
        gml.xbsjFromJSON(obj);
      }

      // 下面是将对象保存在场景树的指令，此功能待定
      // sunwayEarth.sceneTree.root.children.push(new XE.SceneTree.Leaf(json))
      // sunwayEarth.sceneTree.$refs[obj.ref].czmObject.flyTo();
    }

    // 删除文件：服务器、场景、树组件、表格
    function deleteFile(curNode, curData) {
      axios.delete(request.fileSource + curData.id).then((res: any) => {
        if (res.data.success === true) {
          // 从树组件中移除
          const parent = curNode.parent;
          const children = parent.data.children || parent.data;
          const index = children.findIndex((d) => d.id === curData.id);
          children.splice(index, 1);
          // 从场景树中移除
          // let obj = sunwayEarth.sceneTree.$refs[curData.id];
          // sunwayEarth.sceneTree.root.children.remove(obj);
          ElMessage.success(res.data.msg);
        } else {
          ElMessage.error(res.data.msg);
        }
      });
    }

    return {
      ...toRefs(data),
      startLoadFromLocal,
      startLoadFromURL,
      deleteFile,
    };
  },
});
