
/* eslint-disable */
import {
  reactive,
  toRefs,
  onMounted,
  onBeforeUnmount,
  defineComponent,
  getCurrentInstance,
} from "vue";
import axios from "axios";
import { Service } from "~/utils/service";
import { vectorPicker } from "./vectorPicker";
import { ElMessage } from "element-plus";
import { graphic } from "echarts";
export default defineComponent({
  name: "EpsplanetVectorPicker",
  props: vectorPicker,
  setup(props) {
    // 获取earth
    let sunwayEarth: any;
    const { proxy } = getCurrentInstance() as any;
    const thisVue = proxy;
    const data = reactive({
      _disposers: [0],
      _tabledata: [] as any[],
    });
    let handler: any;
    let _czmLabel: any;
    let _dataHeight: any;
    interface tableObject {
      name: any;
      value: any;
    }
    const params = reactive(props.params);
    // const serverWfs = params?.serverWfs ?? reactive(props.serverWfs);
    let serverWfs: string;
    let _testTileset: any;
    data._disposers = [];
    data._tabledata = [];
    onMounted(async () => {
      sunwayEarth = await Service.instance.getEarth();
      await loadconfig();
      if (!handler) {
        initPick();
      } else {
        del();
      }
    });
    onBeforeUnmount(() => {
      del();
    });
    // 加载配置文件
    const loadconfig = async () => {
      await axios
        .get("Epsplanet_Assets/widgets/pick/vectorPicker/config.json")
        .then((res) => {
          serverWfs = res.data.serverWfs;
        })
        .catch((err) => {
          ElMessage.error("加载配置文件失败");
          del();
        });
    };
    function initPick() {
      sunwayEarth.interaction.picking.enabled = true; // 开启拾取操作
      sunwayEarth.czm.viewer.scene.globe.depthTestAgainstTerrain = true; // 开启深度监测
      // 加载测试BIM（此代码测试后需移除）
      // sunwayEarth.sceneTree.root.children.push({
      //   ref: "testTileset",
      //   czmObject: {
      //     xbsjType: "Tileset",
      //     name: "倾斜模型测试1",
      //     url: "http://39.152.31.83:11215/epsplanet/redirect/440a7e9564ea46809d6c0471cf25b84f/文件1/18ccc169ee2d4c79997ec65005abb32d/tileset.json?token=BbNgktJDJkqE2Sj1Iivy0gjQwLIlZAfN",
      //     skipLevelOfDetail: false,
      //   },
      // });
      // _testTileset = sunwayEarth.sceneTree.$refs.testTileset;
      // _testTileset.czmObject.flyTo();
      // 注册点击事件
      handler = new Cesium.ScreenSpaceEventHandler(
        sunwayEarth.czm.viewer.scene.canvas
      );
      // 左键点击获取点坐标
      handler.setInputAction((event: any) => {
        // 清除已有结果
        clear();
        // 进行数据转换
        let positions = convertData(event.position);
        // 查找数据
        findData(positions, serverWfs);
      }, Cesium.ScreenSpaceEventType.LEFT_CLICK);
      // 右键点击退出
      handler.setInputAction((event: any) => {
        del();
      }, Cesium.ScreenSpaceEventType.RIGHT_CLICK);
    }
    //将世界坐标系转为地理坐标系,然后进行缓冲区处理,将点变为面,然后拼接坐标返回字符串
    let convertData = (position: any) => {
      let pickedPosition = sunwayEarth.czm.viewer.scene.pickPosition(position);
      let positionRad = Cesium.Cartographic.fromCartesian(pickedPosition);
      let lng = Cesium.Math.toDegrees(positionRad.longitude);
      let lat = Cesium.Math.toDegrees(positionRad.latitude);
      _dataHeight = positionRad.height;
      let point = turf.point([lng, lat]);
      let buffered = turf.buffer(point, 5, { units: "meters" });
      let positionStr: any = "";
      buffered.geometry.coordinates[0].map((item: any) => {
        positionStr += `${item[0]} ${item[1]},`;
      });
      return positionStr;
    };
    // 通过生成的圆对服务进行检索，找出不相交的数据
    // 得到的数据传递给绘制函数进行数据加载和弹窗的生成
    let findData = async (positions: any, serverUrl: any) => {
      positions = positions.substring(0, positions.length - 1);
      let kill = true;
      for (let i = 0; i < serverUrl.length; i++) {
        if (kill) {
          let axiosUrl: string = `${serverUrl[i]}INTERSECTS(the_geom,SRID=4326;POLYGON((${positions})))`;
          console.log("cql:", axiosUrl);
          await axios
            .get(axiosUrl)
            .then((response) => {
              if (response.status == 200 && response.statusText == "OK") {
                if (response.data.features.length != 0) {
                  let positionData = response.data.features[0].geometry;
                  let propertiesData = response.data.features[0].properties;
                  switchReusltData(positionData, propertiesData);
                  kill = false;
                }
              }
            })
            .catch((e) => {
              console.log(e);
            });
        }
      }
    };
    // 判断传递过来的坐标类型，根据类型对数据做不同处理，生成对应数组
    // 然后生成将其传递给生成图形函数，生成不同形加载到地图上
    // 同时将属性传递给表格函数
    let switchReusltData = (geometry: any, properties: any) => {
      let positions: any[] = [];
      switch (geometry.type) {
        case "MultiPolygon":
          geometry.coordinates[0].forEach((points: any) => {
            points.forEach((point: any) => {
              positions.push(
                Cesium.Math.toRadians(point[0]),
                Cesium.Math.toRadians(point[1])
              );
            });
          });
          graphicalData(positions, "MultiPolygon", properties);
          break;
        case "Polygon":
          geometry.coordinates.forEach((points: any) => {
            points.forEach((point: any) => {
              positions.push(
                Cesium.Math.toRadians(point[0]),
                Cesium.Math.toRadians(point[1])
              );
            });
          });
          graphicalData(positions, "Polygon", properties);
          break;
        case "LineString":
          geometry.coordinates.forEach((point: any) => {
            positions.push([
              Cesium.Math.toRadians(point[0]),
              Cesium.Math.toRadians(point[1]),
            ]);
          });
          graphicalData(positions, "LineString", properties);
          break;
        case "MultiLineString":
          geometry.coordinates[0].forEach((point: any) => {
            positions.push([
              Cesium.Math.toRadians(point[0]),
              Cesium.Math.toRadians(point[1]),
            ]);
          });
          graphicalData(positions, "MultiLineString", properties);
          break;
        default:
          alert("未识别到信息");
          break;
      }
    };
    // 生成图形，加载数据
    let graphicalData = (graphics: any, type: any, popopDatas: any) => {
      let obj: any;
      if (type == "MultiPolygon" || type == "Polygon") {
        let config = {
          ref: "graphicsPickUp",
          czmObject: {
            name: "graphicsPickUp",
            xbsjType: "ClassificationPolygon",
            positions: graphics,
            height: _dataHeight + 50,
            ground: false,
          },
        };
        obj = new XE.Obj.ClassificationPolygon(sunwayEarth)
        obj.xbsjFromJSON(config)

      } else {
        let config = {
          ref: "graphicsPickUp",
          czmObject: {
            name: "graphicsPickUp",
            xbsjType: "Polyline",
            positions: graphics,
            ground: true,
            depthTest: true,
            width: 20,
            material: {
              XbsjColorMaterial: {
                color: [
                  0.9058823529411765, 0.792156862745098, 0.03137254901960784,
                  0.8,
                ],
              },
            },
            evalString: `
              setInterval(()=>{
                p.material.XbsjColorMaterial.color[3] = p.material.XbsjColorMaterial.color[3] == 1 ? 0.8 :1
              },500)
            `,
          },
        };
        obj = new XE.Obj.Polyline(sunwayEarth)
        obj.xbsjFromJSON(config)
      }
      // sunwayEarth.sceneTree.root.children.push(objConfig);
      // _czmLabel = sunwayEarth.sceneTree.$refs.graphicsPickUp;
      const sceneObject = new XE.SceneTree.Leaf(obj);
      sunwayEarth.sceneTree.root.children.push(sceneObject);
      _czmLabel = sceneObject
      popopData(popopDatas);
    };
    // 传入属性，对属性进行重新组合，使其可以传入表单中,在地图弹窗显示
    let popopData = (popupdata: any) => {
      Object.keys(popupdata).forEach((key) => {
        let tableData: tableObject = {
          name: key,
          value: popupdata[key],
        };
        data._tabledata.push(tableData);
      });
      console.log("data._tabledata:", data._tabledata)
      // let time = setTimeout(() => {
      //   let eleTable: any = document.querySelector(".popup");
      //   eleTable.style.display = "block";
      //   clearTimeout(time);
      // }, 500);
    };
    let clear = () => {
      if (_czmLabel) {
        // debugger
        sunwayEarth.sceneTree.root.children.remove(_czmLabel);
        _czmLabel = undefined;
      }
      // let eleTable: any = document.querySelector(".popup");
      // eleTable.style.display = "none";
      data._tabledata = [];
    };
    // 完全清除
    let del = () => {
      if (handler) {
        handler = handler && handler.destroy();
      }
      clear();
      if (_testTileset) {
        sunwayEarth.sceneTree.root.children.remove(_testTileset);
        _testTileset = undefined;
      }
      sunwayEarth.interaction.picking.enabled = false; // 关闭拾取操作
      sunwayEarth.czm.scene.globe.depthTestAgainstTerrain = false; // 关闭深度监测
    };
    const refData = toRefs(data);
    return {
      ...refData,
      initPick,
      del,
    };
  },
});
