<template>
  <div class="epsplanet-panel-base" style="width: 296px">
    <el-form ref="form" label-width="70px">
      <el-form-item :label="label.contrast">
        <el-slider
          v-model="contrast"
          :min="0"
          :max="255"
          :step="1"
          show-input
          :show-input-controls="false"
        ></el-slider>
      </el-form-item>
      <el-form-item :label="label.brightness">
        <el-slider
          v-model="brightness"
          :min="-1"
          :max="1"
          :step="0.1"
          show-input
          :show-input-controls="false"
        ></el-slider>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance,
  watch,
  onBeforeUnmount,
} from "vue";
import { Service } from "~/utils/service";
export default {
  name: "EpsplanetBloom",
  props: [],
  setup() {
    const ctx = getCurrentInstance();
    let sunwayearth;
    const data = reactive({
      label: {
        glowOnly: "仅发光图",
        contrast: "对比度",
        brightness: "亮度",
        delta: "光程差",
        sigma: "sigma",
        stepSize: "stepSize",
      },
      glowOnly: false,
      contrast: 128,
      brightness: -0.3,
      delta: 1.0,
      sigma: 3.78,
      stepSize: 5.0,
    });
    watch(
      () => data.contrast,
      (newVal) => {
        sunwayearth.postProcess.bloom.contrast = newVal;
      }
    );
    watch(
      () => data.brightness,
      (newVal) => {
        sunwayearth.postProcess.bloom.brightness = newVal;
      }
    );
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      sunwayearth.postProcess.bloom.enabled = true;
    });
    onBeforeUnmount(() => {
      sunwayearth.postProcess.bloom.enabled = false;
    });
    const refData = toRefs(data);
    return {
      ...refData,
    };
  },
};
</script>
