
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance,
  onUnmounted,
  watch,
  defineComponent,
  ref,
} from "vue";
import { randomId } from "~/utils/random";
import { buttonProps, buttonEmits } from "./button";
import { panelDock, panelDockCollapse, resizeMapSize } from "~/utils";
// import config from "@/widgets/base/button/config.json";
export default defineComponent({
  name: "EpsplanetButton",
  props: buttonProps,
  emits: buttonEmits,
  components: {},
  //拖拽指令
  directives: {
    drag: {
      // 指令的定义
      mounted(el) {
        let oDiv = el; // 获取当前元素
        let aDiv = el.parentElement.parentElement;
        oDiv.onmousedown = (e: any) => {
          if (el.dataset.dragable == 'false') return;
          // oDiv.style.cursor = "move"; // 鼠标样式
          // 算出鼠标相对元素的位置
          let disX = e.clientX - aDiv.offsetLeft;
          let disY = e.clientY - aDiv.offsetTop;
          document.onmousemove = (e) => {
            // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
            let left = e.clientX - disX;
            let top = e.clientY - disY;
            aDiv.style.left = left + "px";
            aDiv.style.top = top + "px";
          };
          document.onmouseup = (e) => {
            // oDiv.style.cursor = "default";
            document.onmousemove = null;
            document.onmouseup = null;
          };
        };
      },
    },
  },
  setup(props: any, context: any) {
    // console.log(config);
    const ctx = getCurrentInstance();
    //优先从params中取参数
    let params = reactive(props.params) || {};

    //button属性
    let icon = params?.icon ?? reactive(props.icon);
    let btnName = params?.btnName ?? reactive(props.btnName);
    let title = params?.title ?? reactive(props.title);
    let position = params?.position ?? reactive(props.position);
    let type = params?.type ?? reactive(props.type);
    let hasPanel = reactive(props.hasPanel);
    //panel属性
    let panel = params?.panel ?? reactive(props.panel);
    let dockShowTitle = panel?.showTitle;
    let dockMode = panel?.dockMode;
    const data = reactive({
      icon,
      btnName,
      title,
      type,
      panel,
      hasPanel,
      opened: false,
      show: false,
      randomClass: "",
      iconColor: "",
      collapseIcon: "icon-zuo",
      config: {} as any,
      showTitle: true,
      dockShowTitle,
      dockMode,
      isCollapse: true,
    });
    watch(
      () => data.isCollapse,
      (val) => {
        switch (dockMode.toLowerCase()) {
          case "left":
            data.collapseIcon = val ? "icon-zuo" : "icon-you";
            break;
          case "bottom":
            data.collapseIcon = val ? "icon-zhankai" : "icon-shouqi";
            break;

          default:
            break;
        }
      }
    );
    onBeforeMount(() => {
      data.randomClass = randomId();

      if (panel.dockMode) {
        switch (data.dockMode.toLowerCase()) {
          case "left":
            data.collapseIcon = "icon-zuo";
            break;
          case "bottom":
            data.collapseIcon = "icon-zhankai";
            break;

          default:
            break;
        }
        data.showTitle = dockShowTitle;
      } else {
        data.showTitle = true;
      }
    });
    onMounted(() => {
      //按钮位置
      let button = document.getElementsByClassName(
        "epsplanet-button " + data.randomClass
      )[0] as HTMLElement;
      button.style.top = position.top + "px";
      button.style.left = position.left + "px";
    });
    //隐藏弹窗
    function hiddenPanel() {
      data.show = false;
    }
    //销毁弹框
    function closePanel() {
      data.iconColor = "";
      data.opened = false;
      data.show = false;
      if (dockMode) {
        resizeMapSize()
      }
    }
    //打开/显示弹框
    function openPanel() {
      if (data.type == "panel") {
        if (!data.opened) {
          data.iconColor = "aqua";
          data.opened = true;
          data.show = true;

          setTimeout(() => {
            let dom = document.getElementsByClassName("tool-panel " + data.randomClass)[0] as HTMLElement;
            if (panel.dockMode == 'left' || panel.dockMode == 'right') {
              if (panel.size) {
                dom.style.width = panel.size.width + "px";
                dom.style.height = '100%';
              }
              (dom.children[0] as HTMLElement).dataset.dragable = 'false'
              let p = document.getElementsByClassName("epsplanet-popover " + data.randomClass)[0] as HTMLElement;
              panelDock(panel.dockMode, p, window["earth"]);
              data.isCollapse = false;
            } else if (panel.dockMode == 'top' || panel.dockMode == 'bottom') {
              if (panel.size) {
                dom.style.width = '100%';
                dom.style.height = panel.size.height + "px";
              }
              (dom.children[0] as HTMLElement).dataset.dragable = 'false'
              let p = document.getElementsByClassName("epsplanet-popover " + data.randomClass)[0] as HTMLElement;
              panelDock(panel.dockMode, p, window["earth"]);
              data.isCollapse = false;
            }
            else {
              if (panel.size) {
                dom.style.width = panel.size.width + "px";
                dom.style.height = panel.size.height + "px";
              }
              if (panel.position) {
                let pop = dom.parentElement as HTMLElement;
                pop.style.top = panel.position.top + "px";
                pop.style.left = panel.position.left + "px";
              }
            }

            context.emit("click", ctx);
          }, 10);
        } else {
          context.emit("click", ctx);
          data.show = !data.show;
        }
      } else if (data.type == "switch") {
        context.emit("click", ctx);
        data.iconColor = data.iconColor == "" ? "aqua" : "";
        data.opened = !data.opened;
      } else if (data.type == "trigger") {
        context.emit("click", ctx);
        // data.iconColor = data.iconColor == "" ? "aqua" : "";
        let el: any = document.getElementsByClassName(
          "epsplanet-button " + data.randomClass
        )[0].children[0];
        let state = el.style.color == "aqua";
        el.style.color = el.style.color == "" ? "aqua" : "";
        data.iconColor = el.style.color;
        data.opened = false;
        setTimeout(() => {
          data.opened = true;
        }, 10);
      }
    }
    //折叠/展开面板
    function collapse() {
      let p = document.getElementsByClassName(
        "epsplanet-popover " + data.randomClass
      )[0] as HTMLElement;
      panelDockCollapse(dockMode, p, data.isCollapse);
      data.isCollapse = !data.isCollapse;
    }
    const refData = toRefs(data);
    return {
      ...refData,
      openPanel,
      hiddenPanel,
      closePanel,
      collapse,
      ctx,
    };
  },
});
