
import { reactive, toRefs, onMounted, watch, onBeforeUnmount, defineComponent } from "vue";
import { Service } from "~/utils/service";
export default defineComponent({
  name: "EpsplanetBrightness",
  props: [],
  setup() {
    let sunwayearth: any;
    const data = reactive({
      title: "亮度",
      brightness: 0.6,
      min: 0,
      max: 4,
      step: 0.1,
    });
    watch(
      () => data.brightness,
      (newVal) => {
        sunwayearth.postProcess.brightness.brightness = newVal;
      }
    );
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      sunwayearth.postProcess.brightness.enabled = true;
    });
    onBeforeUnmount(() => {
      sunwayearth.postProcess.brightness.enabled = false;
    });
    const refData = toRefs(data);
    return {
      ...refData,
    };
  },
});
