
/* eslint-disable */
import getExtremum from "sdk/XbsjEarth-Plugins/getExtremum";
import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
  watch,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetFlood",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    const thisVue = proxy;
    const data = reactive({
      creating: false,
      editing: false,
      // _waterCzm: undefined,
      waterPositions: [] as any,
      minWaterHeight: 0,
      maxWaterHeight: 0,
      maxHeight: 0,
      minHeight: 0,
      speed: 0.001,
      _waterEntities: undefined,
      timer: undefined,
      entities: [] as any,
      loading: false,
      needTime: "0",
    });
    let _waterCzm: any;
    let _disposers: any = [];
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      init();
    });
    onBeforeUnmount(() => {
      del();
    });

    function init() {
      sunwayearth.czm.viewer.scene.globe.depthTestAgainstTerrain = true;
      let polygon = new XE.Obj.Plots.GeoPolygon(sunwayearth);
      _waterCzm = polygon;
      _disposers = [];
      _disposers.push(
        XE.MVVM.bind(thisVue, "creating", polygon, "creating"),

        XE.MVVM.watch(
          () => polygon.creating,
          async () => {
            console.log(polygon.creating);
            if (!polygon.creating && polygon.positions.length > 2) {
              let positions: any = [];
              for (let i = 0; i < polygon.positions.length; i++) {
                const p = polygon.positions[i];
                positions.push(...p);
              }
              let res = await getExtremum.getExtremum(
                sunwayearth,
                polygon.positions,
                5
              );
              data.waterPositions = positions;
              data.minHeight = Number(res[0].toFixed(3));
              data.maxHeight = Number(res[1].toFixed(3));
              data.maxWaterHeight = data.maxHeight * 2;
              data.minWaterHeight = data.minHeight;
            } else {
              polygon.show = true;
            }
          }
        )
      );
      polygon.creating = true;
      console.log(polygon);
    }

    function getLowerHeight(coordinates: string | any[]) {
      let heightList: any = [];
      for (let i = 2; i < coordinates.length; i += 3) {
        const p = coordinates[i];
        heightList.push(p);
      }
      let minHeight = Math.min(...heightList);
      return Number(minHeight.toFixed(3));
    }
    function drawWater(targetHeight: number, adapCoordi: any, speed: number) {
      // debugger;
      let waterHeight = data.minHeight;

      let entity = sunwayearth.czm.viewer.entities.add({
        polygon: {
          hierarchy: Cesium.Cartesian3.fromRadiansArrayHeights(adapCoordi),
          material: new Cesium.Color.fromBytes(64, 157, 253, 150),
          // perPositionHeight: false,
          extrudedHeight: new Cesium.CallbackProperty(function (
            time: any,
            result: any
          ) {
            // console.log(time, result);
            //此处用属性回调函数，直接设置extrudedHeight会致使闪烁。
            waterHeight += speed / 40;
            if (waterHeight > targetHeight) {
              waterHeight = targetHeight; //给个最大值
            }
            return waterHeight;
          },
          false),
        },
      });

      data._waterEntities = entity;
    }
    function start() {
      // debugger;
      if (data._waterEntities) {
        clearWater();
      }
      computeTime();
      drawWater(data.maxHeight, data.waterPositions, data.speed);
    }
    function clearResult() {
      if (data._waterEntities) {
        sunwayearth.czm.viewer.entities.remove(data._waterEntities);
        data._waterEntities = undefined;
      }
      if (_waterCzm) {
        _waterCzm.show = false;
        _waterCzm._polygon.positions = [];
      }
    }
    function clearWater() {
      if (data._waterEntities) {
        sunwayearth.czm.viewer.entities.remove(data._waterEntities);
        data._waterEntities = undefined;
      }
      // if (_waterCzm) {
      //     _waterCzm.show = false;
      //     _waterCzm._polygon.positions = [];
      // }
    }
    function del() {
      if (data._waterEntities) {
        sunwayearth.czm.viewer.entities.remove(data._waterEntities);
        data._waterEntities = undefined;
      }
      if (_waterCzm) {
        _waterCzm.destroy();
        _waterCzm = undefined;
      }
      if (_disposers) {
        _disposers.forEach((d: () => any) => d());
        _disposers.length = 0;
      }
    }
    function computeTime() {
      let time = (data.maxHeight - data.minHeight) / data.speed;
      //   console.log(time);
      data.needTime = formatSeconds(time);
      console.log(data.needTime);
    }
    function formatSeconds(value: any) {
      var theTime: any = parseInt(value); // 需要转换的时间秒
      var floatSec = (value % 1).toFixed(2);
      console.log(theTime);
      var theTime1 = 0; // 分
      var theTime2 = 0; // 小时
      var theTime3 = 0; // 天
      if (theTime > 60) {
        theTime1 = parseInt(theTime / 60 + "");
        theTime = parseInt((theTime % 60) + "");
        if (theTime1 > 60) {
          theTime2 = parseInt(theTime1 / 60 + "");
          theTime1 = parseInt((theTime1 % 60) + "");
          if (theTime2 > 24) {
            //大于24小时
            theTime3 = parseInt(theTime2 / 24 + "");
            theTime2 = parseInt((theTime2 % 24) + "");
          }
        }
      }
      var result = "";
      if (theTime > 0) {
        result = "" + (parseInt(theTime) + Number(floatSec)) + "秒";
      }
      if (theTime1 > 0) {
        result = "" + parseInt(theTime1 + "") + "分" + result;
      }
      if (theTime2 > 0) {
        result = "" + parseInt(theTime2 + "") + "小时" + result;
      }
      if (theTime3 > 0) {
        result = "" + parseInt(theTime3 + "") + "天" + result;
      }
      return result;
    }
    const refData = toRefs(data);
    return {
      ...refData,
      init,
      getLowerHeight,
      drawWater,
      start,
      clearResult,
      clearWater,
      computeTime,
      formatSeconds,
      del,
    };
  },
});
