
/* eslint-disable */
import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
  watch,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetPotholing",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    const thisVue = proxy;
    const data = reactive({
      name: "未命名开挖分析",
      tilesetList: [{}],
      selectTileset: undefined,
      creating: false,
      editing: false,
      show: true,
      theHeight: 0,
      height: 100,
      deep: 0,
      bottomTextureRotationEditing: false,
      applyToTerrain: false,
      sampledDistance: 10,
      wallImageUrl: "",
      wallImageHeight: 3,
      imageHeight: 3.0,
      wallImageWidthScale: 1,
      bottomImageUrl: "",
      bottomImageHeight: 3,
      bottomImageWidthScale: 1,
      _disposer: [0],
      isSaved: false,
      tips: "shift + 鼠标右键结束绘制",
    });
    let _cutCzmObj: any;
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      init();
    });
    onBeforeUnmount(() => {
      del();
    });
    watch(
      () => data.deep,
      (val) => {
        data.height = data.theHeight - val;
      }
    );
    function init() {
      let cutsurface = new XE.Obj.CutSurface(sunwayearth);
      _cutCzmObj = cutsurface;
      cutsurface.creating = true;
      _cutCzmObj = cutsurface;
      // cutsurface.height = 350;
      data._disposer = [];
      data._disposer.push(
        XE.MVVM.bind(thisVue, "name", cutsurface, "name"),
        XE.MVVM.bind(thisVue, "creating", cutsurface, "creating"),
        XE.MVVM.bind(thisVue, "editing", cutsurface, "editing"),
        XE.MVVM.bind(thisVue, "show", cutsurface, "show"),
        XE.MVVM.bind(thisVue, "height", cutsurface, "height"),
        XE.MVVM.watch(
          () => cutsurface.creating,
          () => {
            if (!cutsurface.creating) {
              data.theHeight = cutsurface.height;
            }
          }
        )
      );
    }

    function xiala() {
      data.tilesetList = [];
      sunwayearth.tilesetCollection.forEach((t: { name: any }, index: any) => {
        data.tilesetList.push({
          name: t.name,
          index: index,
        });
      });
    }
    function changeTileset(t: any) {
      console.log(_cutCzmObj);
      let tileset = sunwayearth.tilesetCollection[t.index];
      sunwayearth.tilesetCollection.forEach(
        (t: any) => (tileset.xbsjFlattenGuid = "")
      );
      tileset.xbsjFlattenGuid = _cutCzmObj.getDiscardGuid();
    }
    function save() {
      data.isSaved = true;
      const sceneObject = new XE.SceneTree.Leaf(_cutCzmObj);
      sunwayearth.sceneTree.root.children.push(sceneObject);
      thisVue.confirmAnalysis(sceneObject);
    }
    function del() {
      if (_cutCzmObj && !data.isSaved) {
        _cutCzmObj.destroy();
        _cutCzmObj = undefined;
      }
      if (data._disposer) {
        data._disposer.forEach((d: any) => d());
        data._disposer.length = 0;
      }
    }
    const refData = toRefs(data);
    return {
      ...refData,
      init,
      xiala,
      changeTileset,
      save,
      del,
    };
  },
});
