
import { onMounted, onBeforeUnmount, defineComponent } from "vue";
import { Service } from "~/utils/service";
export default defineComponent({
  name: "EpsplanetRain",
  props: [],
  setup() {
    let sunwayearth: any;
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      sunwayearth.weather.rainPostProcess.enabled = true;
    });
    onBeforeUnmount(() => {
      sunwayearth.weather.rainPostProcess.enabled = false;
    });
  },
});
