
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance,
  watch,
  onBeforeUnmount,
  computed,
  defineComponent,
} from "vue";
import { Service } from "~/utils/service";
import axios from "axios";
import { ElMessage } from "element-plus";
/* eslint-disable */
export default defineComponent({
  name: "EpsplanetBasemap",
  // props: basemapProps,
  setup(props: any, context) {
    let sunwayearth: any;
    const data = reactive({
      isHover: false,
      basemapList: [
        {
          show: false,
          name: "天地图影像",
          imgUrl: "Epsplanet_Assets/assets/basemap/vector_map.png",
          xbsjType: "Imagery",
          xbsjImageryProvider: {
            XbsjImageryProvider: {
              url: "http://t6.tianditu.com/DataServer?T=img_w&x={x}&y={y}&l={z}&tk=cf7c0b94d43b4d5e1f27a5712b147c70",
              srcCoordType: "WGS84",
              dstCoordType: "WGS84",
            },
          },
        },
        {
          show: true,
          name: "离线影像",
          imgUrl: "Epsplanet_Assets/assets/basemap/Image_map.png",
          xbsjType: "Imagery",
          xbsjImageryProvider: XE.Obj.Imagery.defaultImageryProviderConfig,
        },
        {
          show: false,
          name: "高德影像",
          imgUrl: "Epsplanet_Assets/assets/basemap/Image_map.png",
          xbsjType: "Imagery",
          xbsjImageryProvider: {
            XbsjImageryProvider: {
              url: "http://webst02.is.autonavi.com/appmaptile?style=6&x={x}&y={y}&z={z}",
              srcCoordType: "GCJ02",
              dstCoordType: "WGS84",
            },
          },
        },
      ],
    });
    onBeforeMount(() => {});
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      await loadconfig();
      initfirstmap();
    });

    // 加载配置文件
    const loadconfig = async () => {
      await axios
        .get("Epsplanet_Assets/widgets/base/basemap/config.json")
        .then((res) => {
          data.basemapList = res.data.basemapList;
        })
        .catch((err) => {
          ElMessage.error("加载配置文件失败");
        });
    };

    const overStyle = () => {
      data.isHover = true;
    };
    const leaveStyle = () => {
      data.isHover = false;
    };
    const initfirstmap = () => {
      let result = data.basemapList.filter((basemap) => {
        return basemap?.show == true;
      });
      let map: any;
      //如果配置信息里没有指定的显示图层，则默认选第一个
      if (result.length == 0) {
        map = JSON.parse(JSON.stringify(data.basemapList[0]));
      }
      //如果配置信息里有多个指定的显示图层，则默认选第一个显示图层
      else {
        map = JSON.parse(JSON.stringify(result[0]));
      }
      if (!map.xbsjImageryProvider) {
        map.xbsjImageryProvider = XE.Obj.Imagery.defaultImageryProviderConfig;
      }
      delete map.show;
      if (sunwayearth.sceneTree.root.children.length == 0) {
        let czmObject = {
          czmObject: map,
        };
        let terrain = {
          czmObject: {
            xbsjType: "Terrain",
            xbsjGuid: "d47253ba-f703-4282-92fd-0a5de4ebeb9a",
            name: "未命名地形",
            xbsjTerrainProvider: {
              type: "XbsjCesiumTerrainProvider",
              XbsjEllipsoidTerrainProvider: {},
              XbsjCesiumTerrainProvider: {
                url: "http://www.supermapol.com/realspace/services/3D-stk_terrain/rest/realspace/datas/info/data/path",
                requestVertexNormals: true,
                requestWaterMask: true,
              },
              GoogleEarthEnterpriseTerrainProvider: {},
            },
          },
        };
        sunwayearth.sceneTree.root.children.push(czmObject, terrain);
      } else {
        selectBasemap(map);
      }
    };

    const selectBasemap = (item: any) => {
      data.basemapList.forEach((basemap: any) => {
        basemap.show = false;
      });
      item.show = true;
      data.isHover = false;
      let map = JSON.parse(JSON.stringify(item));
      if (map.show) delete map.show;

      sunwayearth.sceneTree.root.children[0].czmObject = {};
      sunwayearth.sceneTree.root.children[0].czmObject = map;
    };

    onBeforeUnmount(() => {});
    const refData = toRefs(data);
    return {
      ...refData,
      overStyle,
      leaveStyle,
      selectBasemap,
    };
  },
});
