
/* eslint-disable */
import { reactive, toRefs, onMounted, getCurrentInstance, onBeforeUnmount, defineComponent } from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetGroundRoad",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    const thisVue = proxy;
    const data = reactive({
      name:"未命名道路",
      creating: false,
      editing: false,
      width: 0,
      color: "",
      ground: true,
      imgList: [{}],
      pathImg: "Epsplanet_Assets/assets/roadImg/单车道-沥青.jpg",
      _disposer: [0],
      isSaved: false,
    });
    let _roadCzm: any;
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      init();
    });
    onBeforeUnmount(() => {
      del();
    });
    function init() {
      getImgList();
      let roadCzm = new XE.Obj.Road(sunwayearth);
      console.log(roadCzm);
      _roadCzm = roadCzm;
      roadCzm.creating = true;
      console.log(roadCzm);
      data._disposer = [];
      data._disposer.push(
        XE.MVVM.bind(thisVue, "name", roadCzm, "name"),
        XE.MVVM.bind(thisVue, "creating", roadCzm, "creating"),
        XE.MVVM.bind(thisVue, "editing", roadCzm, "editing"),
        XE.MVVM.bind(thisVue, "width", roadCzm, "width"),
        XE.MVVM.bind(thisVue, "pathImg", roadCzm, "imageUrl")
      );
    }
    function getImgList() {
      // console.log(require("assets/roadImg/单车道-沥青.jpg"));
      const imgFilePath = "Epsplanet_Assets/assets/roadImg";
      const files = require.context("@/assets/roadImg");
      data.imgList = [];
      for (let i = 0; i < files.keys().length; i++) {
        const img = files.keys()[i];
        const imgPath = imgFilePath + "/" + img.split("/")[1];
        const imgName = img.split("/")[1].split(".")[0];
        data.imgList.push({
          label: imgName,
          path: imgPath,
        });
      }
      console.log(data.imgList);
    }
    function save() {
      data.isSaved = true;
      const sceneObject = new XE.SceneTree.Leaf(_roadCzm);
      sunwayearth.sceneTree.root.children.push(sceneObject);
      // thisVue.confirm(sceneObject);
    }
    function del() {
      if (_roadCzm && !data.isSaved) {
        _roadCzm.destroy();
        _roadCzm = undefined;
      }
      if (data._disposer) {
        data._disposer.forEach((d: any) => d());
        data._disposer.length = 0;
      }
    }
    const refData = toRefs(data);
    return {
      ...refData,
      init,
      save,
      del,
    };
  },
});
