
/* eslint-disable */
import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
  watch,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetPoint",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    const thisVue = proxy;
    const data = reactive({
      name: "未命名点标绘",
      creating: false,
      makiIconList: [{}],
      makiIcon: "",
      text: "",
      size: 48,
      fillColor: "rgba(255,255,255,1)",
      OutlineColor: "rgba(6,131,6,1)",
      extText: "",
      extTextPosition: "",
      extTextTrue: "",
      extTextFont: "48px 楷体",
      extTextFontFamily: "",
      extTextFontSize: "",
      extTextPixelOffset: [20, 40],
      fontFamily: ["楷体", "宋体", "黑体", "微软雅黑"],
      _disposers: [0],
      isSaved: false,
    });
    let _pinCzm: any;
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      init();
    });
    onBeforeUnmount(() => {
      del();
    });
    watch(
      () => data.extText,
      (val) => {
        data.extTextTrue = val + data.extTextPosition;
      }
    );
    watch(
      () => data.extTextPosition,
      (val) => {
        data.extTextTrue = val + data.extText;
      }
    );
    function init() {
      let pin = new XE.Obj.Pin(sunwayearth);
      console.log(pin);
      pin.pinBuilder.extTextPixelOffset = [20, 40];
      _pinCzm = pin;
      pin.creating = true;
      let makiIconList = XE.Obj.Pin.MakiIcon;
      for (const key in makiIconList) {
        if (Object.hasOwnProperty.call(makiIconList, key)) {
          const value = makiIconList[key];
          data.makiIconList.push({
            label: key,
            value: value,
          });
        }
      }
      data._disposers = [];
      data._disposers.push(
        XE.MVVM.bind(thisVue, "creating", pin, "creating"),
        XE.MVVM.bind(thisVue, "name", pin, "name"),
        XE.MVVM.bind(thisVue, "text", pin.pinBuilder, "text"),
        XE.MVVM.bind(thisVue, "size", pin.pinBuilder, "size"),
        XE.MVVM.bind(thisVue, "makiIcon", pin.pinBuilder, "makiIcon"),
        XE.MVVM.bind(thisVue, "extTextTrue", pin.pinBuilder, "extText"),
        XE.MVVM.bind(thisVue, "extTextFont", pin.pinBuilder, "extTextFont"),
        XE.MVVM.bind(
          thisVue,
          "extTextPixelOffset",
          pin.pinBuilder,
          "extTextPixelOffset"
        ),
        XE.MVVM.watch(
          () => [...pin.position],
          () => {
            // console.log([...pin.position]);
            let pos = [...pin.position];
            data.extTextPosition = `\n经度:${((pos[0] / Math.PI) * 180).toFixed(
              6
            )}°\n纬度:${((pos[1] / Math.PI) * 180).toFixed(6)}°`;
            // this.extTextPosition = `\n经度:${(pos[0]/Math.PI*180).toFixed(3)}°\n纬度:${(pos[1]/Math.PI*180).toFixed(3)}°\n高度:${pos[2].toFixed(3)}米`;
          }
        )
      );
    }

    function changeFillColor(val: string) {
      let v = val.split("(")[1].split(")")[0].split(",");
      let cc: any = [];
      v.forEach((c: any, i: number) => {
        if (i == 3) {
          cc.push(Number(c));
        } else {
          cc.push(Number(c) / 255.0);
        }
      });
      _pinCzm.pinBuilder.fillColor = cc;
    }
    function changeOutlineColor(val: string) {
      let v = val.split("(")[1].split(")")[0].split(",");
      let cc: any = [];
      v.forEach((c: any, i: number) => {
        if (i == 3) {
          cc.push(Number(c));
        } else {
          cc.push(Number(c) / 255.0);
        }
      });
      _pinCzm.pinBuilder.outlineColor = cc;
    }
    function del() {
      if (data._disposers) {
        data._disposers.forEach((d: any) => d());
        data._disposers.length = 0;
      }

      if (_pinCzm && !data.isSaved) {
        _pinCzm.destroy();
        _pinCzm = undefined;
      }
    }
    function save() {
      data.isSaved = true;
      const sceneObject = new XE.SceneTree.Leaf(_pinCzm);
      sunwayearth.sceneTree.root.children.push(sceneObject);
      // thisVue.confirm(sceneObject);
    }
    const refData = toRefs(data);
    return {
      ...refData,
      _pinCzm,
      init,
      changeFillColor,
      changeOutlineColor,
      del,
      save,
    };
  },
});
