
import { reactive, toRefs, onMounted, defineComponent } from "vue";
import { Service } from "~/utils/service";
import { changeBtnState } from "~/utils/domTool";

export default defineComponent({
  name: "EpsplanetScreenshot",
  props: ["btnClass"],
  setup(props) {
    let sunwayEarth: any;
    const data = reactive({});
    onMounted(async () => {
      sunwayEarth = await Service.instance.getEarth();
      // sunwayEarth.cameraViewManager.globe.flyTo();
      screenshot();
      changeBtnState(props.btnClass, false);
    });
    function screenshot() {
      // 截图并发送
      sunwayEarth
        .capture(1920, 1080)
        .then((img) => {
          //通过a标签下载base64图片
          let a = document.createElement("a");
          a.href = img;
          a.download = "截图.png";
          a.click();
        })
        .catch((err) => {
          console.log(err);
        });
    }
    return {
      ...toRefs(data)
    };
  },
});
