import {
  reactive,
  toRefs,
  defineComponent,
  getCurrentInstance,
  onMounted,
  onBeforeUnmount,
} from "vue";
import { Service } from "~/utils/service";
export default defineComponent({
  name: "EpsplanetRectangle",
  props: ["open"],
  setup() {
    let sunwayEarth: any;
    const { proxy } = getCurrentInstance() as any;
    const thisVue = proxy;
    const data = reactive({
      text: "",
      outlineWidth: 2,
      outlineShow: true,
      ground: true,
      outlineColor: "rgba(0,255,0,1)",
      inlineColor: "rgba(0,255,0,0.1)",
      editing: false,
    })

    onMounted(async () => {
      sunwayEarth = await Service.instance.getEarth();
      init();
    })
    onBeforeUnmount(() => {
      del();
    })

    function init() {
      thisVue._polygonCzm = new XE.Obj.Plots.GeoRectangle(sunwayEarth);
      thisVue.pin = new XE.Obj.Plots.GeoPin(sunwayEarth);
      thisVue._polygonCzm.creating = true;
      thisVue._disposers = [];
      thisVue._disposers.push(
        XE.MVVM.bind(thisVue, "name", thisVue._polygonCzm, "name"),
        XE.MVVM.bind(thisVue, "editing", thisVue._polygonCzm, "editing"),
        XE.MVVM.bind(thisVue, "outlineShow", thisVue._polygonCzm, "outlineShow"),
        XE.MVVM.bind(thisVue, "outlineWidth", thisVue._polygonCzm, "outlineWidth"),
        XE.MVVM.bind(thisVue, "ground", thisVue._polygonCzm, "ground"),
        XE.MVVM.bind(thisVue, "editing", thisVue._polygonCzm, "editing"),
        XE.MVVM.watch(
          () => [...thisVue._polygonCzm.positions],
          () => {
            console.log([...thisVue._polygonCzm.positions]);
            thisVue.pin.position = thisVue._polygonCzm.positions[thisVue._polygonCzm.positions.length - 1];
            if (thisVue._polygonCzm.positions.length == 1) {
              thisVue.pin.innerHTML =
                '<div style="cursor:pointer;position:absolute;width:300px;left:6px;line-height:15px;color:white;"><span style="padding:2px;border-radius:2px;text-stroke:2px #000;font-size:14px;color:#ffffff;background-color:rgba(50,50,50,0.5)">点击左键确定矩形中心点</span></div>';
            } else if (thisVue._polygonCzm.positions.length == 2) {
              thisVue.pin.innerHTML =
                '<div style="cursor:pointer;position:absolute;width:300px;left:6px;line-height:15px;color:white;"><span style="padding:2px;border-radius:2px;text-stroke:2px #000;font-size:14px;color:#ffffff;background-color:rgba(50,50,50,0.5)">移动鼠标确定方向和对角线长度，再次点击左键创建完成</span></div>';
            } else if (thisVue._polygonCzm.positions.length == 3) {
              thisVue.pin.enabled = false;
            }
          }
        )
      );
      // window._polygonCzm = thisVue._polygonCzm;
      // window.pin = thisVue.pin;
      // window.sunwayearth = sunwayEarth;
    }

    function capture() {
      //截图并发送
      sunwayEarth
        .capture(1920, 1080)
        .then((img) => {
          thisVue.image = img;
          //通过a标签下载base64图片
          let a = document.createElement("a");
          a.href = thisVue.image;
          a.download = "截图.png";
          a.click();
        })
        .catch((err) => {
          console.log(err);
        });
    }

    function changeColor(val) {
      let v = val.split("(")[1].split(")")[0].split(",");
      let cc: any = [];
      v.forEach((c, i) => {
        if (i == 3) {
          cc.push(Number(c));
        } else {
          cc.push(Number(c) / 225.0);
        }
      });
      thisVue._polygonCzm.color = cc;
    }

    function changeOutlineColor(val) {
      let v = val.split("(")[1].split(")")[0].split(",");
      let cc: any = [];
      v.forEach((c, i) => {
        if (i == 3) {
          cc.push(Number(c));
        } else {
          cc.push(Number(c) / 225.0);
        }
      });
      thisVue._polygonCzm.outlineColor = cc;
    }

    function del() {
      if (thisVue.pin) {
        thisVue.pin.destroy();
      }
      if (thisVue._disposers) {
        thisVue._disposers.forEach((d) => d());
        thisVue._disposers.length = 0;
      }
      if (thisVue._polygonCzm && !thisVue.isSaved) {
        thisVue._polygonCzm.destroy();
        thisVue._polygonCzm = undefined;
      }
    }

    function save() {
      thisVue.isSaved = true;
      const sceneObject = new XE.SceneTree.Leaf(thisVue._polygonCzm);
      sunwayEarth.sceneTree.root.children.push(sceneObject);
      // thisVue.confirm(sceneObject);
    }

