    // function startup() {
    //   earth = sunwayEarth;

    //   earth.sceneTree.root = {
    //     children: [
    //       {
    //         czmObject: {
    //           name: "谷歌影像",
    //           xbsjType: "Imagery",
    //           xbsjImageryProvider: {
    //             XbsjImageryProvider: {
