
import { reactive, toRefs, onMounted, defineComponent, ref } from "vue";
import { toolbarProps } from "./toolbar";
export default defineComponent({
  name: "EpsplanetToolbar",
  props: toolbarProps,
  setup(props: any) {
    let toolbar: any = ref(null);
    const isCollapse = ref(false);
    const mode = reactive(props.mode);
    const collapsable = reactive(props.collapsable);
    const position = reactive(props.position);
    const style = position;
    let height: any;
    const data = reactive({
      mode,
      style,
      collapsable,
      collapseIcon: "icon-zhankai",
    });
    onMounted(() => {
      height = toolbar.value.clientHeight - 10;
    });
    const refData = toRefs(data);
    function collapse() {
      if (!toolbar.value.style.height) {
        toolbar.value.style.height = height + "px";
      }
      setTimeout(() => {
        if (isCollapse.value) {
          data.collapseIcon = "icon-zhankai";
          toolbar.value.style.height = height + "px";
        } else {
          data.collapseIcon = "icon-shouqi";
          toolbar.value.style.height = "10px";
        }
        isCollapse.value = !isCollapse.value;
      }, 10);
    }
    return {
      ...refData,
      toolbar,
      collapse,
      isCollapse,
    };
  },
});
