
/* eslint-disable */
import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetPolygon",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    const thisVue = proxy;
    const data = reactive({
      name:"未命名多边形",
      creating: false,
      editing: false,
      outlineShow: true,
      outlineWidth: 0,
      color: "rgba(0,255,0,0.1)",
      outlineColor: "rgba(0,255,0,1)",
      ground: true,
      _disposer: [0],
      isSaved: false,
    });
    let _polygonCzm: any;
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      init();
    });
    onBeforeUnmount(() => {
      del();
    });
    function init() {
      let polygonCzm = new XE.Obj.Plots.GeoPolygon(sunwayearth);
      _polygonCzm = polygonCzm;
      polygonCzm.creating = true;
      console.log(polygonCzm);
      data._disposer = [];
      data._disposer.push(
        XE.MVVM.bind(thisVue, "name", polygonCzm, "name"),
        XE.MVVM.bind(thisVue, "creating", polygonCzm, "creating"),
        XE.MVVM.bind(thisVue, "editing", polygonCzm, "editing"),
        XE.MVVM.bind(thisVue, "outlineShow", polygonCzm, "outlineShow"),
        XE.MVVM.bind(thisVue, "outlineWidth", polygonCzm, "outlineWidth"),
        XE.MVVM.bind(thisVue, "ground", polygonCzm, "ground"),
        XE.MVVM.watch(
          () => polygonCzm.creating,
          () => {
            data.creating = polygonCzm.creating;
          }
        ),
        XE.MVVM.watch(
          () => polygonCzm.editing,
          () => {
            data.editing = polygonCzm.editing;
          }
        )
      );
    }

    function changeColor(val: string) {
      let v = val.split("(")[1].split(")")[0].split(",");
      let cc: any = [];
      v.forEach((c: any, i: number) => {
        if (i == 3) {
          cc.push(Number(c));
        } else {
          cc.push(Number(c) / 255.0);
        }
      });
      _polygonCzm.color = cc;
    }
    function changeOutlineColor(val: string) {
      let v = val.split("(")[1].split(")")[0].split(",");
      let cc: any = [];
      v.forEach((c: any, i: number) => {
        if (i == 3) {
          cc.push(Number(c));
        } else {
          cc.push(Number(c) / 255.0);
        }
      });
      _polygonCzm.outlineColor = cc;
    }
    function save() {
      data.isSaved = true;
      const sceneObject = new XE.SceneTree.Leaf(_polygonCzm);
      sunwayearth.sceneTree.root.children.push(sceneObject);
      // thisVue.confirm(sceneObject);
    }
    function del() {
      if (_polygonCzm && !data.isSaved) {
        _polygonCzm.destroy();
        _polygonCzm = undefined;
      }
      if (data._disposer) {
        data._disposer.forEach((d: any) => d());
        data._disposer.length = 0;
      }
    }
    const refData = toRefs(data);
    return {
      ...refData,
      init,
      changeColor,
      changeOutlineColor,
      save,
      del,
    };
  },
});
