import { App } from 'vue'



//导出基础组件
import * as base from './components/base/export'
// 导出空间分析组件
import * as analysis from './components/analysis/export'
//导出标绘组件
import * as plot from './components/plot/export'
//导出特效组件
import * as effect from './components/effect/export'
//导出测量组件
import * as measure from './components/measure/export'
// 导出拾取组件
import * as pick from './components/pick/export'
// 导出路径飞行组件
import * as path from './components/path/export'
// 导出多屏组件
import * as multiView from './components/multiView/export'
// 导出其他组件
import * as others from './components/others/export'
// 导出视角组件
import * as camera from './components/camera/export'
//导出所有方法
export * from './utils'
export * from './components/base/panel/src/usePanel'
//导出样式
import './theme/index.scss'
import './assets/font/iconfont.css'
// 所有组件列表
const components: any[] = [

    ...base.default,
    ...analysis.default,
    ...plot.default,
    ...effect.default,
    ...measure.default,
    ...pick.default,
    ...path.default,
    ...multiView.default,
    ...others.default,
    ...camera.default
]

// 定义 install 方法， App 作为参数
const install = (app: App): void => {
    // 遍历注册所有组件
    components.map((component) => app.component(component.name, component))
}


export default {
    install,
}
