
/* eslint-disable */
import {
  reactive,
  toRefs,
  onMounted,
  onBeforeUnmount,
  defineComponent,
} from "vue";
import { Service } from "~/utils/service";
import axios from "axios";
import { ElMessage } from "element-plus";

export default defineComponent({
  name: "EpsplanetGeneralQuery",
  setup() {
    let sunwayEarth: any;
    const data = reactive({
      _disposers: [] as any,
      attributes: [] as any,
      curLayer: "",
      curAttr: "",
      curAttrName: "",
      currentPage: 1,
      fuzzy: true,
      layers: [] as any,
      loading: false,
      pageSize: 10,
      pageData: [] as any,
      services: [] as any,
      searchResultData: [] as any,
      selectedAttributes: [] as any,
      showSelectedAttrOnly: false,
      tabledata: [] as any,
      total: 0,
    });
    interface tableObject {
      name: any;
      value: any;
    }
    let handler: any;
    let _czmObj: any;
    let _testTileset: any;

    onMounted(async () => {
      sunwayEarth = await Service.instance.getEarth();
      // 加载配置文件
      await axios
        .get("Epsplanet_Assets/widgets/others/generalQuery/config.json")
        .then((res) => {
          data.services = res.data.services;
          initPick();
        })
        .catch((err) => {
          ElMessage.error("加载配置文件失败");
          del();
        });
    });
    onBeforeUnmount(() => {
      del();
    });

    function initPick() {
      sunwayEarth.interaction.picking.enabled = true; // 开启拾取操作
      sunwayEarth.czm.viewer.scene.globe.depthTestAgainstTerrain = true; // 开启深度监测
      // 加载测试BIM（此代码测试后需移除）
      sunwayEarth.sceneTree.root.children.push({
        ref: "testTileset",
        czmObject: {
          xbsjType: "Tileset",
          name: "倾斜模型测试1",
          url:
            "http://39.152.31.83:11215/epsplanet/redirect/440a7e9564ea46809d6c0471cf25b84f/文件1/18ccc169ee2d4c79997ec65005abb32d/tileset.json?token=BbNgktJDJkqE2Sj1Iivy0gjQwLIlZAfN",
          skipLevelOfDetail: false,
        },
      });
      _testTileset = sunwayEarth.sceneTree.$refs.testTileset;
      _testTileset.czmObject.flyTo();
    }
    
    // 获取wfs服务的字段
    let getLayerAttributes = () => {
      data.curAttr = "";
      if (data.layers.length) {
        data.layers = [];
      }
      if (data.attributes.length) {
        data.attributes = [];
      }
      data.services.forEach((element) => {
        element.layers.forEach((layer) => {
          data.layers.push(layer);
          if (layer.name === data.curLayer) {
            if (layer.showSelectedAttrOnly) {
              data.showSelectedAttrOnly = layer.showSelectedAttrOnly
              data.selectedAttributes = layer.selectedAttributes
            }
            layer.searchKeys.forEach((item) => {
              data.attributes.push(item);
            });
          }
        });
      });
      // console.log(data.attributes);
    };
    // 通过CQL语句对服务进行过滤，得到的数据传递给绘制函数进行数据加载和弹窗的生成
    let findDataByAttribute = () => {
      data.loading = true;
      clear();
      let filterStr: string = ``;
      data.attributes.forEach((attr, index) => {
        if (attr.input) {
          if (data.fuzzy) {
            filterStr += `${attr.fieldName}%20LIKE%20%27%25${attr.input}%25%27%20`
          } else {
            filterStr += `${attr.fieldName}=%27${attr.input}%27%20`
          }
          
        } else {
          filterStr += `1=1%20`
        }
        if (index < data.attributes.length - 1) {
          filterStr += `AND%20`
        }
      })
      let kill = true;
      data.services.forEach(async (item) => {
        if (kill) {
          let axiosUrl: string = `
          ${item.url}/${item.namespace}/wfs?service=wfs
          &request=GetFeature&version=1.1.0
          &outputFormat=application/json
          &TYPENAME=${item.namespace}:${data.curLayer}
          &CQL_FILTER=${filterStr}
          `;
          await axios
            .get(axiosUrl)
            .then((response) => {
              if (response.status == 200 && response.statusText == "OK") {
                if (response.data.features.length !== 0) {
                  // console.log("response.data.features", response.data.features);
                  data.searchResultData = response.data.features;
                  data.total = response.data.features.length;
                  changePage(1);
                  createInfoTable()
                  kill = false;
                } else {
                  data.total = 0;
                }
              }
            })
            .catch((e) => {
              console.log(e);
              ElMessage.error(e);
            });
        }
      });
      data.loading = false;
    };
    // 判断传递过来的坐标类型，根据类型对数据做不同处理，生成对应数组
    // 然后生成将其传递给生成图形函数，生成不同形加载到地图上
    // 同时将属性传递给表格函数
    let switchResultData = (geometry, properties) => {
      let positions: any[] = [];
      switch (geometry.type) {
        case "MultiPolygon":
          geometry.coordinates[0].forEach((points) => {
            points.forEach((point) => {
              positions.push(
                Cesium.Math.toRadians(point[0]),
                Cesium.Math.toRadians(point[1])
              );
            });
          });
          graphicalData(positions, "MultiPolygon", properties);
          break;
        case "Polygon":
          geometry.coordinates.forEach((points) => {
            points.forEach((point) => {
              positions.push(
                Cesium.Math.toRadians(point[0]),
                Cesium.Math.toRadians(point[1])
              );
            });
          });
          graphicalData(positions, "Polygon", properties);
          break;
        case "LineString":
          geometry.coordinates.forEach((point) => {
            positions.push([
              Cesium.Math.toRadians(point[0]),
              Cesium.Math.toRadians(point[1]),
            ]);
          });
          graphicalData(positions, "LineString", properties);
          break;
        case "MultiLineString":
          geometry.coordinates[0].forEach((point) => {
            positions.push([
              Cesium.Math.toRadians(point[0]),
              Cesium.Math.toRadians(point[1]),
            ]);
          });
          graphicalData(positions, "MultiLineString", properties);
          break;
        default:
          ElMessage.warning("未读取到图形信息");
          break;
      }
    };
    // 生成图形，加载数据
    let graphicalData = (pos, type, props) => {
      let objConfig: any = {};
      if (type === "MultiPolygon" || type === "Polygon") {
        objConfig = {
          ref: "graphicsPickUp",
          czmObject: {
            name: "graphicsPickUp",
            xbsjType: "ClassificationPolygon",
            positions: pos,
            // clampToGround: true,
            // height: _dataHeight + 50,
            ground: true,
            depthTest: true,
          },
        };
      } else {
        objConfig = {
          ref: "graphicsPickUp",
          czmObject: {
            name: "graphicsPickUp",
            xbsjType: "Polyline",
            positions: pos,
            ground: true,
            depthTest: true,
            width: 20,
            material: {
              XbsjColorMaterial: {
                color: [
                  0.9058823529411765,
                  0.792156862745098,
                  0.03137254901960784,
                  0.8,
                ],
              },
            },
            evalString: `
              setInterval(()=>{
                p.material.XbsjColorMaterial.color[3] = p.material.XbsjColorMaterial.color[3] == 1 ? 0.8 :1
              },500)
            `,
          },
        };
      }
      sunwayEarth.sceneTree.root.children.push(objConfig);
      _czmObj = sunwayEarth.sceneTree.$refs.graphicsPickUp;
      _czmObj.czmObject.flyTo()
    };

    // 点击某条查询结果时，显示对应图形，并弹窗显示属性
    let clickOneData = (currentRow, oldCurrentRow) => {
      clearCzmObj()
      console.log(currentRow)
      switchResultData(currentRow.geometry, currentRow);
      popupData(currentRow);
    }

    // 传入属性，对属性进行重新组合，使其可以传入表单中, 在地图弹窗显示
    let popupData = (props) => {
      data.tabledata = [];
      if (data.showSelectedAttrOnly) {
        data.selectedAttributes.forEach(attr => {
          let tableDataOne: tableObject = {
          name: attr.title,
          value: props[attr.fieldName],
        };
        data.tabledata.push(tableDataOne);
        })
      } else {
        Object.keys(props).forEach((key) => {
          let tableDataOne: tableObject = {
            name: key,
            value: props[key],
          };
          data.tabledata.push(tableDataOne);
        });
      }
      showInfoTable()
    };

    // 创建弹窗DOM元素
    let createInfoTable = () => {
      let infoTable = document.createElement("div")
      // infoTable.setAttribute("class", "popup")
      infoTable.setAttribute("v-show", "tabledata.length > 0")
      infoTable.id = "general-query-popup"
      infoTable.innerHTML = `
        <table></table>
      `
      sunwayEarth.czm.viewer.container.appendChild(infoTable)
      // document.getElementsByClassName("el-main")[0].appendChild(infoTable)
    }

    let showInfoTable = () => {
      let infoTable = document.getElementById("general-query-popup") as HTMLBodyElement
      if (infoTable) {
        let table = infoTable.getElementsByTagName("table")[0] as HTMLTableElement
        for (let i = table.rows.length - 1; i >= 0; i--) {
          table.deleteRow(i);
        }
        data.tabledata.forEach((item) => {
          let newRow = table.insertRow();
          let cell1 = newRow.insertCell();
          let cell2 = newRow.insertCell();
          cell1.innerHTML = item.name;
          cell2.innerHTML = item.value;
        })
        infoTable.style.display = "block"
      }
    }

    let closeInfoTable = () => {
      let infoTable = document.getElementById("general-query-popup")
      if (infoTable) {
        infoTable.style.display = "none"
      }
    }

    // 移除弹窗元素
    let removeInfoTable = () => {
      let infoTable = document.getElementById("general-query-popup")
      if (infoTable) {
        sunwayEarth.czm.viewer.container.removeChild(infoTable)
      }
    }

    // 清除数据和场景树中的图形
    let clear = () => {
      clearCzmObj()
      data.searchResultData = [];
      data.pageData = [];
      data.tabledata = [];
    };

    // 清除场景树中的图形
    let clearCzmObj = () => {
      if (_czmObj) {
        sunwayEarth.sceneTree.root.children.remove(_czmObj);
        _czmObj = undefined;
      }
    }

    // 分页
    let changePage = (currentPage) => {
      data.currentPage = currentPage;
      let pageSize = data.pageSize;
      let start = (currentPage - 1) * pageSize;
      let end = currentPage * pageSize;
      for (let i = start; i < end; i++) {
        if (data.searchResultData[i]) {
          data.searchResultData[i].properties.geometry = 
          data.searchResultData[i].geometry
          data.pageData.push(data.searchResultData[i].properties)
        }
      }
      // data.pageData = data.searchResultData.slice(start, end);
    }

    // 完全清除
    let del = () => {
      if (handler) {
        handler = handler && handler.destroy();
      }
      clear();
      removeInfoTable()
      if (_testTileset) {
        sunwayEarth.sceneTree.root.children.remove(_testTileset);
        _testTileset = undefined;
      }
      sunwayEarth.interaction.picking.enabled = false; // 关闭拾取操作
      sunwayEarth.czm.scene.globe.depthTestAgainstTerrain = false; // 关闭深度监测
    };

    return {
      ...toRefs(data),
      getLayerAttributes,
      findDataByAttribute,
      clickOneData,
      changePage,
      closeInfoTable,
    };
  },
});
