import { Service } from "~/utils/service";
import { useStore } from "vuex";
export default defineComponent({
  name: "EpsplanetCircle",
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    const thisVue = proxy;
    const data = reactive({
      destroyArray: [""],
      color: "rgba(0,255,0,0.1)",
      outlineColor: "rgba(0,255,0,1)",
      creating: false,
      editing: false,
      isSaved: false,
      formRef: "",
      form: {
        name: "",
        show: true,
        outlineshow: true,
        ground: true,
        width: 2,
      },
    });

    const methods = {
      init() {
        thisVue._circleDraw = new XE.Obj.Plots.GeoCircle(sunwayearth);

        thisVue._circleDraw.creating = true;
        thisVue.pin = new XE.Obj.Plots.GeoPin(sunwayearth);
        data.destroyArray = [];
        data.destroyArray.push(
          XE.MVVM.bind(thisVue, "form.name", thisVue._circleDraw, "name"),
          XE.MVVM.bind(thisVue, "editing", thisVue._circleDraw, "editing"),
          XE.MVVM.bind(
            thisVue,
            "form.outlineshow",
            thisVue._circleDraw,
            "outlineShow"
          ),
          // XE.MVVM.bind(thisVue, "form.width", _circleDraw, "outlineWidth"),
          XE.MVVM.bind(thisVue, "form.ground", thisVue._circleDraw, "ground"),
          XE.MVVM.bind(
            thisVue,
            "form.width",
            thisVue._circleDraw,
            "outlineWidth"
          ),
          XE.MVVM.watch(
            () => [...thisVue._circleDraw.positions],
            () => {
              thisVue.pin.position =
                thisVue._circleDraw.positions[
                  thisVue._circleDraw.positions.length - 1
                ];
              if (thisVue._circleDraw.positions.length == 1) {
                thisVue.pin.innerHTML =
                  '<div style="cursor:pointer;position:absolute;width:300px;left:6px;line-height:15px;color:white;"><span style="padding:2px;border-radius:2px;text-stroke:2px #000;font-size:14px;color:#ffffff;background-color:rgba(50,50,50,0.5)">点击左键确定圆心位置</span></div>';
              } else if (thisVue._circleDraw.positions.length == 2) {
                thisVue.pin.innerHTML =
                  '<div style="cursor:pointer;position:absolute;width:300px;left:6px;line-height:15px;color:white;"><span style="padding:2px;border-radius:2px;text-stroke:2px #000;font-size:14px;color:#ffffff;background-color:rgba(50,50,50,0.5)">点击左键确定圆半径</span></div>';
              } else if (thisVue._circleDraw.positions.length == 3) {
                thisVue.pin.enabled = false;
              }
            }
          )
        );
      },
      del() {
        if (data.destroyArray) {
          data.destroyArray.forEach((d: any) => d());
          data.destroyArray.length = 0;
        }
        if (thisVue._circleDraw && !data.isSaved) {
          thisVue._circleDraw.destroy();
          thisVue._circleDraw = undefined;
        }
        thisVue.pin.enabled = false;
      },
      changeColor(val) {
        let v = val.split("(")[1].split(")")[0].split(",");
        let cc: any[] = [];
        v.forEach((c, i) => {
          if (i == 3) {
            cc.push(Number(c));
          } else {
            cc.push(Number(c) / 255.0);
          }
        });
        thisVue._circleDraw.color = cc;
      },
      changeOutlineColor(val) {
        let v = val.split("(")[1].split(")")[0].split(",");
        let cc: any[] = [];
        v.forEach((c, i) => {
          if (i == 3) {
            cc.push(Number(c));
          } else {
            cc.push(Number(c) / 255.0);
          }
        });
        thisVue._circleDraw.outlineColor = cc;
      },
      save() {
        data.isSaved = true;
        const sceneObject = new XE.SceneTree.Leaf(thisVue._circleDraw);
        sunwayearth.sceneTree.root.children.push(sceneObject);
        // thisVue.confirm(sceneObject);
      },
    };
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      methods.init();
    });
    onBeforeUnmount(() => {
      methods.del();
    });
    return { ...toRefs(data), ...methods };
  },
