      chooseLayerOne() {
        data.layerOption = sunwayearth.sceneTree.root.children.filter(
          (element) => {
            return element.enabled;
          }
        );
        data.xbsjRightBottomView = true;
        data.xbsjRightTopView = true;
        let czmType: any = "";
        if (data.radio1 == "1") {
          sunwayearth.sceneTree.root.children.forEach((element) => {
            if (element.czmObject.xbsjGuid == data.layerID) {
              _leftczmObj = element.czmObject;
              czmType = element.czmObject.xbsjType;
            }
          });
          if (czmType == "Imagery") {
            _leftczmObj.splitDirection = "ImagerySplitDirection.LEFT";
          } else {
            data.destroyArray.push(
              XE.MVVM.bind(
                thisVue,
                "xbsjRightTopView",
                _leftczmObj,
                "xbsjRightTopView"
              ),
              XE.MVVM.bind(
                thisVue,
                "xbsjRightBottomView",
                _leftczmObj,
                "xbsjRightBottomView"
              )
            );
            data.xbsjRightBottomView = false;
            data.xbsjRightTopView = false;
          }

          _leftczmObj.flyTo();
        } else {
          sunwayearth.sceneTree.root.children.forEach((element) => {
            if (element.czmObject.xbsjGuid == data.layerID) {
              _topczmObj = element.czmObject;
            }
          });
