          _topczmObj.flyTo();
        }
      },
      chooseLayerTwo() {
        data.layerOption = sunwayearth.sceneTree.root.children.filter(
          (element) => {
            return element.enabled;
          }
        );
        data.xbsjLeftTopView = true;
        data.xbsjLeftBottomView = true;
