
/* eslint-disable */
import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetVisual",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    const thisVue = proxy;
    const data = reactive({
      twoPostionsEditing: false,
      positionEditing: false,
      rotationEditing: false,
      fovH: 80,
      fovV: 60,
      _disposer: [] as any,
    });
    let _viewshed: any;
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      init();
    });
    onBeforeUnmount(() => {
      del();
    });
    function init() {
      sunwayearth.czm.viewer.scene.globe.depthTestAgainstTerrain = true;
      let viewshed = new XE.Obj.Viewshed(sunwayearth);
      _viewshed = viewshed;
      console.log(viewshed);
      data._disposer = [];

      data._disposer.push(
        XE.MVVM.bind(
          thisVue,
          "twoPostionsEditing",
          viewshed,
          "twoPostionsEditing"
        ),
        // XE.MVVM.bind(thisVue, "positionEditing", viewshed, "positionEditing"),
        XE.MVVM.bind(thisVue, "rotationEditing", viewshed, "rotationEditing"),
        XE.MVVM.bindDegreeRadian(thisVue, "fovH", viewshed, "fovH"),
        XE.MVVM.bindDegreeRadian(thisVue, "fovV", viewshed, "fovV"),
        XE.MVVM.watch(
          () => viewshed.twoPostionsEditing,
          () => {
            data.twoPostionsEditing = viewshed.twoPostionsEditing;
            console.log(viewshed)
            // if(viewshed.twoPostionsEditing) {
            //   viewshed.position = [];
            //   viewshed.rotation = [];
            // }
          }
        ),
        XE.MVVM.watch(
          () => viewshed.positionEditing,
          () => {
            data.positionEditing = viewshed.positionEditing;
          }
        ),
        XE.MVVM.watch(
          () => viewshed.rotationEditing,
          () => {
            data.rotationEditing = viewshed.rotationEditing;
          }
        )
      );
    }

    function setAsCamera() {
      if (_viewshed) {
        _viewshed.setPositionWithCurrentCamera();
      }
    }
    function del() {
      if (_viewshed) {
        _viewshed.destroy();
        _viewshed = undefined;
      }
      if (data._disposer) {
        data._disposer.forEach((d: any) => d());
        data._disposer.length = 0;
      }
    }
    const refData = toRefs(data);
    return {
      ...refData,
      init,
      setAsCamera,
      del,
    };
  },
});
