
import { onMounted, onBeforeUnmount, defineComponent } from "vue";
import { Service } from "~/utils/service";
export default defineComponent({
  name: "EpsplanetNightvision",
  props: [],
  setup() {
    let sunwayearth: any;

    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      sunwayearth.postProcess.nightVision.enabled = true;
    });
    onBeforeUnmount(() => {
      sunwayearth.postProcess.nightVision.enabled = false;
    });
  },
});
