export class Service {

  static instance: Service;
  private name: string;
  private earth: any;
  private hasEarth: boolean = false;

  private constructor(name: string) {
    this.name = name
    // this.instance = null
  }

  public static getInstance(): Service {
    if (this.instance == null) {
      console.log('create new')
      this.instance = new Service('earth')

    }
    return Service.instance
  }


  public setEarth(earth: any): void {
    this.earth = earth
    this.hasEarth = true;
  }
  public getEarth() {
    return new Promise(resolve => {
      let times = 0
      let itv = setInterval(() => {
        if (times == 50) {
          clearInterval(itv)
          resolve(false)
        }
        if (this.hasEarth) {
          clearInterval(itv)
          let interval = setInterval(() => {
            if (this.earth) {
              clearInterval(interval)
              resolve(this.earth)
            }
          }, 100)
        }
        times++
      }, 100)
    })
  }
  public destroyEarth(earth: any) {
    this.earth = earth && earth.destroy();
    this.hasEarth = false;
  }
}

// export  Service