
import {
  reactive,
  toRefs,
  onMounted,
  getCurrentInstance,
  onBeforeUnmount,
  defineComponent,
  computed,
} from "vue";
import { Service } from "~/utils/service";

export default defineComponent({
  name: "EpsplanetStatusBar",
  props: [],
  setup() {
    const { proxy } = getCurrentInstance() as any;
    let sunwayearth: any;
    const thisVue = proxy;
    const data = reactive({
      showLon: true,
      showLat: true,
      showCamera: true,
      showHeight: true,
      showFps: true,
      showHeading: true,
      showPitch: true,
      showRoll: true,
      showVelocity: true,
      showMouse: true,
      longitudeString: "",
      latitudeString: "",
      heightString: "",
      headingString: "",
      pitchString: "",
      rollString: "",
      cameraString: "",
      fpsString: "",
      baseVelocity: 0.0,
      velocity: 0.0,
      velocityRatio: 1.0,
      lang: {
        longitude: "经度",
        latitude: "纬度",
        height: "高度",
        fps: "帧率",
        heading: "偏航角",
        pitch: "俯仰角",
        roll: "翻滚角",
        meter: "米",
        velocity: "键盘运动速度",
        createPolylineTip: "左键添加点，右键删除点，shift+右键创建完成",
        editPolylineTip:
          "鼠标移动到其中一个点会出现一个操作栏，点击移动按钮，可以移动折线位置；点击增加按钮，可以在该位置增加一个点；点击删除按钮，可以删除该点",
        createRectangleTip:
          "点击左键确定矩形中心点，移动鼠标确定矩形方向和对角线长度，再次点击左键创建完成",
        editRectangleTip: "左键点击其中一个点移动矩形位置",
        createCircleTip:
          "点击左键确定圆的圆心，移动鼠标确定圆的半径，再次点击左键创建完成",
        editCircleTip: "左键点击其中一个点移动圆位置",
        createDoubleArrowTip: "在四个不同位置点击左键创建双箭头",
        editDoubleArrowTip: "左键点击其中一个点移动双箭头位置",
        createFlattenedPolygonTip:
          "左键添加点，右键删除点，shift+右键停止绘制，上下移动鼠标确定高度，再次点击左键创建完成",
        editFlattenedPolygonTip:
          "鼠标移动到其中一个点会出现一个操作栏，点击移动按钮，可以移动折线位置；点击增加按钮，可以在该位置增加一个点；点击删除按钮，可以删除该点",
        movableObjectTip:
          "鼠标左键点击坐标轴x,y,z任意一个轴，轴变黄色，可沿着相应位置进行移动，再次点击，轴恢复原色，停止移动，点击右键坐标轴消失",
        rotatableObjectTip:
          "鼠标左键点击旋转坐标轴任意一个轴，轴变黄色，可沿着相应位置进行旋转，再次点击，轴恢复原色，停止旋转，点击右键旋转坐标轴消失",
        positionPickingTip: "点击鼠标左键拾取位置",
      },
      mousePos: {
        lng: 0,
        lat: 0,
        height: 0,
      },
      mouseString: "",
    });
    let _disposers: any = [];
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      init();
    });
    onBeforeUnmount(() => {
      del();
    });
    const velocityString = computed(() => {
      return ` ${data.lang.velocity}: ${data.velocity.toFixed(
        1
      )} km/h (${data.baseVelocity.toFixed(1)} × ${data.velocityRatio.toFixed(
        1
      )})`;
    });
    function init() {
      let _scene = sunwayearth.czm.scene;
      const scratchCartograghic = [0, 0, 0];
      sunwayearth.czm.viewer.canvas.addEventListener(
        "mousemove",
        (mouseEvent: any) => {
          const carto = sunwayearth.pickPosition(
            {
              x: mouseEvent.offsetX,
              y: mouseEvent.offsetY,
            },
            scratchCartograghic
          );
          if (carto) {
            data.mousePos.lng = (carto[0] * 180) / Math.PI;
            data.mousePos.lat = (carto[1] * 180) / Math.PI;
            data.mousePos.height = carto[2];
            data.mouseString =
              "鼠标位置:" +
              data.mousePos.lng.toFixed(6) +
              "° " +
              data.mousePos.lat.toFixed(6) +
              "° " +
              data.mousePos.height.toFixed(2) +
              "m";
          } else {
            data.mouseString = "暂时无法获取鼠标位置...";
          }
        }
      );
      let _camera = sunwayearth.czm.camera;

      const td = Cesium.Math.toDegrees;

      const updateCameraString = () => {
        const camera = _camera;

        var l = td(camera.positionCartographic.longitude).toFixed(5);
        var b = td(camera.positionCartographic.latitude).toFixed(5);
        var h = camera.positionCartographic.height.toFixed(2);
        var y = td(camera.heading).toFixed(2);
        var p = td(camera.pitch).toFixed(2);
        var r = td(camera.roll).toFixed(2);

        data.cameraString = `${data.lang.longitude}: ${l}° ${data.lang.latitude}: ${b}°`;
        data.longitudeString = `${data.lang.longitude}: ${l}°`;
        data.latitudeString = `${data.lang.latitude}: ${b}°`;
        data.heightString = `${data.lang.height}: ${h}${data.lang.meter}`;
        data.headingString = `${data.lang.heading}: ${y}°`;
        data.pitchString = `${data.lang.pitch}: ${p}°`;
        data.rollString = `${data.lang.roll}: ${r}°`;

        // this.cameraString = `${data.lang.longitude}: ${l}° ${data.lang.latitude}: ${b}° ${data.lang.height}: ${h}${data.lang.meter} ${data.lang.heading}: ${y}° ${data.lang.pitch}: ${p}° ${data.lang.roll}: ${r}°`;
      };

      _disposers = [];
      _disposers.push(_camera.changed.addEventListener(updateCameraString));
      updateCameraString();

      // 帧率的计算借助了Cesium中的东西，需要开启debugShowFramesPerSecond
      _scene.debugShowFramesPerSecond = true;
      const tempDisposer = _scene._postRender.addEventListener(() => {
        tempDisposer();
        _scene._performanceContainer.style.visibility = "hidden"; // 隐藏默认的帧率显示窗口
      });

      _disposers.push(
        _scene._postRender.addEventListener(() => {
          if (_scene._performanceDisplay) {
            data.fpsString = `${data.lang.fps}: ${_scene._performanceDisplay._fpsText.nodeValue} `;
          } else {
            data.fpsString = "";
          }
        })
      );

      _disposers.push(
        XE.MVVM.track(
          thisVue,
          "baseVelocity",
          sunwayearth.camera.immersion,
          "baseVelocity"
        )
      );
      _disposers.push(
        XE.MVVM.track(
          thisVue,
          "velocity",
          sunwayearth.camera.immersion,
          "velocity"
        )
      );
      _disposers.push(
        XE.MVVM.bind(
          thisVue,
          "velocityRatio",
          sunwayearth.camera.immersion,
          "velocityRatio"
        )
      );
    }

    function del() {
      if (_disposers) {
        _disposers.forEach((d: any) => d());
        _disposers.length = 0;
      }
    }
    const refData = toRefs(data);
    return {
      ...refData,
      velocityString,
      init,
      del,
    };
  },
});
