
import { onMounted, onBeforeUnmount, defineComponent } from "vue";
import { Service } from "~/utils/service";
import { changeBtnState } from "~/utils/domTool";

export default defineComponent({
  name: "EpsplanetMeasureHeight",
  props: ["btnClass"],
  setup(props) {
    let sunwayearth: any;
    let _disposer: any = [];
    onMounted(async () => {
      sunwayearth = await Service.instance.getEarth();
      sunwayearth.analyzation.measurement.type = "TRIANGLE_DISTANCE";
      _disposer.push(
        XE.MVVM.watch(
          () => sunwayearth.analyzation.measurement.type,
          (newValue: any) => {
            if (newValue == "NONE") {
              changeBtnState(props.btnClass, false);
            }
          }
        )
      );
    });
    onBeforeUnmount(() => {
      _disposer.forEach((d: any) => d());
    });
  },
});
