
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance,
  watch,
  onBeforeUnmount,
  computed,
  defineComponent,
} from "vue";
import { panelProps } from "./panel";
import { randomId } from "~/utils/random";
import { panelDock, panelDockCollapse, resizeMapSize } from "~/utils";
export default defineComponent({
  name: "EpsplanetPanel",
  props: panelProps,
  directives: {
    drag: {
      // 指令的定义
      mounted(el) {
        let oDiv = el; // 获取当前元素
        let aDiv = el.parentElement.parentElement;
        oDiv.onmousedown = (e: any) => {
          if (el.dataset.dragable == 'false') return;
          // oDiv.style.cursor = "move"; // 鼠标样式
          // 算出鼠标相对元素的位置
          let disX = e.clientX - aDiv.offsetLeft;
          let disY = e.clientY - aDiv.offsetTop;
          document.onmousemove = (e) => {
            // 用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
            let left = e.clientX - disX;
            let top = e.clientY - disY;
            aDiv.style.left = left + "px";
            aDiv.style.top = top + "px";
          };
          document.onmouseup = (e) => {
            // oDiv.style.cursor = "default";
            document.onmousemove = null;
            document.onmouseup = null;
          };
        };
      },
    },

  },
  setup(props: any, context) {
    const { proxy } = getCurrentInstance() as any;
    const title = reactive(props.title);
    let panel = reactive(props.panel);
    let dockShowTitle = panel?.showTitle;
    let dockMode = panel?.dockMode;
    const data = reactive({
      title,
      panel,
      opened: false,
      show: true,
      randomClass: "",
      collapseIcon: "icon-zuo",
      showTitle: true,
      dockShowTitle,
      dockMode,
      isCollapse: true,
    });

    let _earth;
    watch(
      () => data.opened,
      (newVal) => {
        if (newVal) {
          setTimeout(() => {
            let dom = document.getElementsByClassName("tool-panel " + data.randomClass)[0] as HTMLElement;
            if (panel.dockMode == 'left' || panel.dockMode == 'right') {
              if (panel.size) {
                dom.style.width = panel.size.width + "px";
                dom.style.height = '100%';
              }
              (dom.children[0] as HTMLElement).dataset.dragable = 'false'
              let p = document.getElementsByClassName("epsplanet-popover " + data.randomClass)[0] as HTMLElement;
              panelDock(panel.dockMode, p, window["earth"]);
              data.isCollapse = false;
            } else if (panel.dockMode == 'top' || panel.dockMode == 'bottom') {
              if (panel.size) {
                dom.style.width = '100%';
                dom.style.height = panel.size.height + "px";
              }
              (dom.children[0] as HTMLElement).dataset.dragable = 'false'
              let p = document.getElementsByClassName("epsplanet-popover " + data.randomClass)[0] as HTMLElement;
              panelDock(panel.dockMode, p, window["earth"]);
              data.isCollapse = false;
            }
            else {
              if (panel.size) {
                dom.style.width = panel.size.width + "px";
                dom.style.height = panel.size.height + "px";
              }
              if (panel.position) {
                let pop = dom.parentElement as HTMLElement;
                pop.style.top = panel.position.top + "px";
                pop.style.left = panel.position.left + "px";
              }
            }
          }, 10);
        }
      }
    );
    watch(
      () => data.isCollapse,
      (val) => {
        switch (dockMode.toLowerCase()) {
          case "left":
            data.collapseIcon = val ? "icon-zuo" : "icon-you";
            break;
          case "bottom":
            data.collapseIcon = val ? "icon-zhankai" : "icon-shouqi";
            break;

          default:
            break;
        }
      }
    );
    onBeforeMount(() => {
      data.randomClass = randomId();
      if (panel.dockMode) {
        switch (data.dockMode.toLowerCase()) {
          case "left":
            data.collapseIcon = "icon-zuo";
            break;
          case "bottom":
            data.collapseIcon = "icon-zhankai";
            break;

          default:
            break;
        }
        data.showTitle = dockShowTitle;
      } else {
        data.showTitle = true;
      }
    });
    onMounted(() => {
      console.log(props.title, panel);
    });
    onBeforeUnmount(() => {
      //   earth.weather.cloud.enabled = false;
    });
    //隐藏弹窗
    function hiddenPanel() {
      data.show = false;
    }
    //销毁弹框
    function closePanel() {
      data.opened = false;
      data.show = false;
      if (dockMode) {
        resizeMapSize()
      }
    }
    //折叠/展开面板
    function collapse() {
      let p = document.getElementsByClassName(
        "epsplanet-popover " + data.randomClass
      )[0] as HTMLElement;
      panelDockCollapse(dockMode, p, data.isCollapse);
      data.isCollapse = !data.isCollapse;
    }

    const refData = toRefs(data);
    return {
      ...refData,
      closePanel,
      hiddenPanel,
      collapse
    };
  },
});
