
/* eslint-disable */
import {
  reactive,
  toRefs,
  defineComponent,
  getCurrentInstance,
  onMounted,
  onBeforeUnmount,
} from "vue";
import { Service } from "~/utils/service";
import { getDisAndLabelPos } from "~/utils/measure";
import { changeBtnState } from "~/utils/domTool";
export default defineComponent({
  name: "EpsplanetGroundDistance",
  props: ["btnClass"],
  setup(props) {
    let sunwayEarth: any;
    const { proxy } = getCurrentInstance() as any;
    const thisVue = proxy;
    const data = reactive({
      _polylineCzm: undefined,
      // creating: false,
      editing: false,
      width: 5,
      color: "rgba(0,255,0,1)",
      ground: true,
      _disposer: [],
      isSaved: false,
      _disGroundLabels: [],
      disGroundinterval: 10,
      // timeOrigin: 5, //50毫秒来一次
      // time: 5, //50毫秒来一次
      // timerReady: true,
    })
    onMounted(async () => {
      sunwayEarth = await Service.instance.getEarth();
      sunwayEarth.analyzation.measurement.clearGroundDistance = () => del();
      init();
    })
    onBeforeUnmount(() => {
      del();
    })
    function init() {
      // thisVue.date = new Date();
      thisVue._disGroundLabels = [];
      thisVue._polylineCzm = new XE.Obj.Plots.GeoPolyline(sunwayEarth);
      thisVue._polylineCzm.creating = true;
      thisVue.pin = new XE.Obj.Plots.GeoPin(sunwayEarth);
      thisVue.pins = [];
      // 加载测试BIM（此代码测试后需移除）
      // sunwayEarth.sceneTree.root.children.push({
      //   ref: "testTileset",
      //   czmObject: {
      //     xbsjType: "Tileset",
      //     name: "倾斜模型测试1",
      //     url:
      //       "http://39.152.31.83:11215/epsplanet/redirect/440a7e9564ea46809d6c0471cf25b84f/文件1/18ccc169ee2d4c79997ec65005abb32d/tileset.json?token=BbNgktJDJkqE2Sj1Iivy0gjQwLIlZAfN",
      //     skipLevelOfDetail: false,
      //   },
      // });
      // let testTileset = sunwayEarth.sceneTree.$refs.testTileset;
      // testTileset.czmObject.flyTo();

      thisVue._disposers = [];
      thisVue._disposers.push(
        // XE.MVVM.bind(thisVue, "creating", thisVue._polylineCzm, "creating"),
        XE.MVVM.bind(thisVue, "editing", thisVue._polylineCzm, "editing"),
        XE.MVVM.bind(thisVue, "width", thisVue._polylineCzm, "width"),
        XE.MVVM.bind(thisVue, "ground", thisVue._polylineCzm, "ground"),
        XE.MVVM.watch(
          () => [...thisVue._polylineCzm.positions],
          () => {
            updateMeasure(thisVue._polylineCzm.positions);
            // if (thisVue.timerReady) {
            //   thisVue.timerReady = false;
            //   updateMeasure(thisVue._polylineCzm.positions);
            //   thisVue.interval = setInterval(() => {
            //     thisVue.time -= 1;
            //     if (thisVue.time <= 0) {
            //       thisVue.time = thisVue.timeOrigin;
            //       thisVue.timerReady = true;
            //       clearInterval(thisVue.interval);
            //     }
            //   }, 1); //100毫秒
            // }
          }
        ),
        XE.MVVM.watch(() => thisVue._polylineCzm.creating, 
        () => changeBtnState(props.btnClass, false)
        )
      );
    }
    function changeColor(val) {
      let v = val.split("(")[1].split(")")[0].split(",");
      let cc: any = [];
      v.forEach((c, i) => {
        if (i == 3) {
          cc.push(Number(c));
        } else {
          cc.push(Number(c) / 225.0);
        }
      });
      thisVue._polylineCzm.color = cc;
    }
    function updateMeasure(p) {
      if (thisVue._disGroundLabels) {
        thisVue._disGroundLabels.forEach((l) => l.destroy());
        thisVue._disGroundLabels = [];
      }
      
      if (p.length === 1) {
        let l = {
          dis: "左键添加点, 右键删除点, shift+右键完成",
          pos: p[0],
        };
        let lb = createLabel(l);
        thisVue._disGroundLabels.push(lb);
      } else if (p.length > 1) {
        let it = thisVue.disGroundinterval;
        let result = getDisAndLabelPos(p, it, sunwayEarth);
        if (!result) {
          return;
        }
        thisVue._result = result;
        let labels = result.label;

        labels.forEach((l) => {
          let lb = createLabel(l);
          thisVue._disGroundLabels.push(lb);
        });
      }
    }
    function createLabel(option) {
      let p = new XE.Obj.Plots.GeoPin(sunwayEarth);
      p.innerHTML =
        '<div style="cursor:pointer;position: absolute;width:300px;left:6px; line-height:15px;color: white;">' +
        '<span style="padding:2px;border-radius: 2px;text-stroke:2px #000;font-size: 14px;color:#ffffff;background-color: rgba(50,50,50,0.5)">' +
        option.dis +
        "</span></div>";
      p.position = option.pos;
      p._pin.show = false;
      return p;
    }
    function del() {
      if (thisVue._disposers) {
        thisVue._disposers.forEach((d) => d());
        thisVue._disposers.length = 0;
      }
      if (thisVue._polylineCzm && !thisVue.isSaved) {
        thisVue._polylineCzm.destroy();
        thisVue._polylineCzm = undefined;
      }
      if (thisVue._disGroundLabels) thisVue._disGroundLabels.forEach((l) => l.destroy());
      thisVue._disGroundLabels = [];
    }
    function save() {
      thisVue.isSaved = true;
      let sceneObject = new XE.SceneTree.Leaf(thisVue._polylineCzm);
      sunwayEarth.sceneTree.root.children.push(sceneObject);
      thisVue.confirm(sceneObject);
    }
    // function closeWin() {
    //   thisVue.open = false;
    // }
    // function distance(point1, point2) {
    //   var point1cartographic = Cesium.Cartographic.fromRadians(point1[0], point1[1], point1[2]);
    //   var point2cartographic = Cesium.Cartographic.fromRadians(point2[0], point2[1], point2[2]);
    //   /**根据经纬度计算出距离**/
    //   var geodesic = new Cesium.EllipsoidGeodesic();
    //   geodesic.setEndPoints(point1cartographic, point2cartographic);
    //   var s = geodesic.surfaceDistance;
    //   //console.log(Math.sqrt(Math.pow(distance, 2) + Math.pow(endheight, 2)));
    //   //返回两点之间的距离
    //   s = Math.sqrt(Math.pow(s, 2) + Math.pow(point2cartographic.height - point1cartographic.height, 2));
    //   return s;
    // }
    return {
      ...toRefs(data),
      changeColor,
      save,
    }
  }
})
